/* eslint-disable import/no-unresolved */
import React, { Component } from 'react';
import { store, registerNameSpace, connect, executeTimesLogger } from 'core';

import { fromJS, List as ImList, Map } from 'immutable';
import { List } from '../../base';
import './style.less';
import { FORM_NAMESPACE, FORM_MOBILE_CONSTS, MODULE_SEPARATOR } from './constants';
import FormItem from './FormItem';

const { registerAction, dispatch, getState } = registerNameSpace(FORM_NAMESPACE);

// 以下state都是Immutable类型的数据
registerAction({
	/**
	 * 获取 moduleIds 对应的表单数据
	 * @param {*} state
	 * @param { Object } configs
	 * 		config 参数 {String | Array} moduleIds 表单id或id数组
	 * @returns {Object} {
	 *  moduleId1: [{
	 *    itemValue: {
	 *      value: 1, display: 1
	 *    },
	 *    ...otherProps
	 *  }
	 * }]
	 */
	getAllFormValue(state, { moduleIds }) {
		if (typeof moduleIds === 'string') {
			moduleIds = [moduleIds];
		}
		let formValueObject = {};
		const metaStored = store.getState()['meta'];
		moduleIds.map(moduleId => {
			let returnKey = moduleId;
			if (Array.isArray(moduleId)) {
				returnKey = moduleId.slice(1);
			}
			const moduleType = metaStored.getIn([moduleId, FORM_MOBILE_CONSTS.MODULE_TYPE]);
			formValueObject[returnKey] = { areaType: moduleType };
			formValueObject[returnKey].values =
				__getFormItemPropByCode({ moduleId, formModuleId: moduleId, key: 'itemValue' }) || {};
		});
		const formRelations = __immutableToJS(metaStored.getIn(['formrelation'])) || {};
		Object.entries(formRelations).map(([mainKey, relatedKeys]) => {
			formValueObject[mainKey] = formValueObject[mainKey] || {};
			const mainKeyInfos = formValueObject[mainKey];
			mainKeyInfos.values = mainKeyInfos.values || {};
			let mainKeyValues = mainKeyInfos.values;
			relatedKeys.map(relatedKey => {
				const relatedKeyInfos = formValueObject[relatedKey] || {};
				const relatedKeyValues = relatedKeyInfos.values || {};
				mainKeyInfos.values = {
					...mainKeyValues,
					...relatedKeyValues
				};
				delete formValueObject[relatedKey];
			});
		});
		Object.entries(formValueObject).map(([moduleId, moduleInfo]) => {
			const { areaType, values } = moduleInfo;
			moduleInfo = {
				areaType,
				areacode: moduleId
			};
			if (areaType === 'form') {
				const status = state.getIn([moduleId, FORM_MOBILE_CONSTS.BACKEND_STATUS]);
				moduleInfo.rows = [{ values, status }];
			} else {
				moduleInfo.rows = values;
			}
			formValueObject[moduleId] = moduleInfo;
		});
		return formValueObject;
	},

	/**
	 * 已修改
	 * 获取 moduleId 对应表单的当前状态: 'edit'/'browse'/'disabled'
	 * @param {*} state
	 * @param {*} moduleId 表单id
	 */
	getFormStatus(state, { moduleId, formId }) {
		const formModuleId = __getFormModuleId(moduleId, formId);
		return state.getIn([formModuleId, 'areaStatus']);
	},

	/**
	 * 获取表单某些字段的值
	 * @param {*} state
	 * @param {*} moduleId 表单id
	 * @param {String | Array} attrCodes 属性code或其数组
	 * @returns {Object | Array} 根据attrCodes是否为数组, 返回对应的结果: { value: 'aa', display: 'bb' } 或 [{ value: 'aa1', display: 'bb1' }, { value: 'aa2', display: 'bb2' }]
	 */
	getFormItemsValue(state, { moduleId, formId, attrCodes }) {
		return __getFormItemsProps({ moduleId, formId, key: 'itemValue', attrCodes });
	},

	/**
	 * 获取表单某些字段的禁用
	 * @param {*} state
	 * @param {*} moduleId 同上
	 * @param {String | Array} attrCodes 同上
	 * @returns {Boolean | Array} true | [true, false]
	 */
	getFormItemsDisabled(state, { moduleId, formId, attrCodes }) {
		return __getFormItemsProps({ moduleId, formId, key: 'disabled', attrCodes });
	},

	/**
	 * 获取表单某些字段的必输性
	 * @param {*} state
	 * @param {*} moduleId 同上
	 * @param {String | Array} attrCodes 同上
	 * @returns {Boolean | Array} true | [true, false]
	 */
	getFormItemsRequired(state, { moduleId, formId, attrCodes }) {
		return __getFormItemsProps({ moduleId, formId, key: 'required', attrCodes });
	},
	// })();

	// registerAction({

	/**
	 *
	 * @param {immutable} state
	 * @param {Object} data
	 */
	setAllFormValue(state, data) {
		if (Object.prototype.toString.call(data) !== '[object Object]') {
			console.log('setAllFormValue 参数错误');
			return state;
		}
		const metaStored = store.getState()['meta'];
		const formRelations = __immutableToJS(metaStored.getIn(['formrelation'])) || {};
		Object.entries(data).map(([moduleId, { rows }]) => {
			try {
				const relatedKeys = formRelations[moduleId];
				const mainModuleData = rows[0].values;
				// 存储关联表格的字段关系
				const relatingKeyMap = {};
				if (Array.isArray(relatedKeys) && relatedKeys.length) {
					relatedKeys.map(relatedKey => {
						relatingKeyMap[relatedKey] = {};
						const attrsOfRelatedModule = __getAttrcodesOfModule(metaStored, relatedKey);
						if (Array.isArray(attrsOfRelatedModule) && attrsOfRelatedModule.length) {
							attrsOfRelatedModule.map(attr => {
								relatingKeyMap[relatedKey][attr] = mainModuleData[attr];
							});
						}
					});
				}
				let dataToSet = rows[0];
				const { values: moduleDataValues, status } = dataToSet;
				// 设置传输结构中的表单 status 和 表单 id
				if (__formAttributeValid(status)) {
					state = state.setIn([moduleId, FORM_MOBILE_CONSTS.BACKEND_STATUS], status);
				}
				// 设置各字段值
				Object.entries(moduleDataValues).map(([attrcode, itemValue]) => {
					state = __setFormItemPropByCode(state, moduleId, attrcode, 'itemValue', fromJS(itemValue));
				});
				Object.entries(relatingKeyMap).map(([relatedModuleKey, attrItems]) => {
					Object.entries(attrItems).map(([attrcode, itemValue]) => {
						state = __setFormItemPropByCode(
							state,
							relatedModuleKey,
							attrcode,
							'itemValue',
							fromJS(itemValue)
						);
					});
				});
			} catch (error) {
				console.log('setAllFormValue 参数错误, moduleId: ', moduleId, 'errorMessage: ', error);
			}
		});
		return dispatch(state);
	},

	/**
	 * 设置整单状态, 不影响每个表单项单独的 disabled 与否, 需要单独设置
	 * @param {*} state
	 * @param {String} moduleIds
	 * @param {String} status 'edit'/'browse'/'disabled'
	 */
	setFormStatus(state, moduleIds, status) {
		if (!Array.isArray(moduleIds)) {
			moduleIds = [moduleIds];
		}
		moduleIds.map(moduleId => {
			state = state.setIn([moduleId, 'areaStatus'], status);
		});
		return dispatch(state);
	},

	/**
	 * 设置表单某些字段的值
	 * @param {*} state
	 * @param {String} moduleId
	 * @param {Object} values {
	 *    name: {
	 *      value: '张三',
	 *      display: null
	 *    },
	 *    sex: {
	 *      display: '男',
	 *      value: '0'
	 *    }
	 *  }
	 */
	setFormItemsValue(state, moduleId, values) {
		return dispatch(__setFormItemsProps(state, moduleId, 'itemValue', values));
	},

	/**
	 * 设置表单某些字段的禁用
	 * @param {*} state
	 * @param {*} moduleId
	 * @param {*} values { name: true, sex: false }
	 */
	setFormItemsDisabled(state, moduleId, values) {
		return dispatch(__setFormItemsProps(state, moduleId, 'disabled', values));
	},

	/**
	 * 设置表单某些字段的必输性
	 * @param {*} state
	 * @param {*} moduleId
	 * @param {*} values { name: true, sex: false }
	 */
	setFormItemsRequired(state, moduleId, values) {
		return dispatch(__setFormItemsProps(state, moduleId, 'required', values));
	},

	/**
	 * 设置表单某些字段的浏览态或编辑态, true 为编辑态
	 * @param {*} state
	 * @param {*} moduleId
	 * @param {*} values 同上
	 */
	setFormItemsEditable(state, moduleId, values) {
		return dispatch(__setFormItemsProps(state, moduleId, 'editable', values));
	},

	setFormItemsVisible(state, moduleId, values) {
		const visibleField = state.getIn([moduleId, 'visibleField']);
		return dispatch(__setFormItemsProps(state, moduleId, visibleField, values));
	},

	emptyAllFormValue(state, moduleId) {
		let metaItems = store.getState()['meta'].getIn([moduleId, 'items']);
		if (!metaItems) {
			return state;
		}
		const nullObject = Map().set('display', null).set('value', null);
		metaItems.map(item => {
			state = state.setIn([moduleId, 'formItems', item.get('attrcode'), 'itemValue'], nullObject);
		});
		dispatch(state);
	},

	isCheckNow(state, moduleIds) {
		const formValid = __validateRequired(moduleIds);
		return formValid;
	},

	/**
	 * for form item
	 */
	/**
	 *
	 * @param {*} state
	 * @param {String} attrcode
	 * @param {Immutable} value
	 */
	setValueByAttrcode(state, { moduleId, formModuleId, attrcode, value }) {
		return dispatch(state.setIn([formModuleId || moduleId, 'formItems', attrcode, 'itemValue'], fromJS(value)));
	},

	// 设置指定form的rowdata
	setFormRowData(state, { moduleId, rowData }) {
		const { rowid, values } = rowData;
		const formModuleId = __getFormModuleId(moduleId, rowid);
		dispatch(__setFormItemsProps(state, formModuleId, 'itemValue', values));
	},

	/**
	 * 物料参照新增行
	 */
	pushRows(state, { moduleId, rows }) {
		let concatRows;
		if (Array.isArray(rows)) {
			concatRows = fromJS(rows);
		} else {
			concatRows = ImList();
		}
		const resultingRows = state.getIn([moduleId, 'tableRows']) || ImList();
		return dispatch(state.setIn([moduleId, 'tableRows'], resultingRows.concat(concatRows)));
	},

	/**
	 * 同一模板生成多个表单时, 取值api
	 * @param { String } moduleId 模板区域Id
	 * @param { String | Array | undefined } formIds 表单id, 不传时获取所有moduleId匹配的数据
	 * @returns { Array } 数据数组, 数据结构如:
	 * 	[{
	 * 		formId: '1',
	 * 		values: {
	 * 			name: { display: 1, value: 1 },
	 * 			age: { display: 2, value: 2 },
	 * 		}
	 * 	}]
	 */
	getFormValuesById(state, { moduleId, formIds }) {
		if (!formIds) {
			formIds = [];
			state.map((formData, storedModuleId) => {
				const moduleIdInfo = storedModuleId.split(MODULE_SEPARATOR);
				if (moduleIdInfo[0] === moduleId && moduleIdInfo[1]) {
					formIds.push(moduleIdInfo[1]);
				}
			});
		} else if (typeof formIds === 'string') {
			formIds = [formIds];
		}
		let formValueArr = [];
		formIds.map(formId => {
			const formModuleId = __getFormModuleId(moduleId, formId);
			const values = __getFormItemPropByCode({
				moduleId,
				formModuleId,
				key: 'itemValue'
			});
			formValueArr.push({
				formId,
				values
			});
		});
		return formValueArr;
	},

	/**
	 * 同一模板生成多个表单时, 设值api
	 * @param {*} moduleId
	 * @param {*} data 设置的数据, 数据结构如:
	 * 	[{
	 * 		formId: '1',
	 * 		values: {
	 * 			name: { display: 1, value: 1 },
	 * 			age: { display: 2, value: 2 },
	 * 		}
	 * 	}]
	 */
	setFormValuesById(state, { moduleId, data }) {
		if (!Array.isArray(data)) {
			console.log('setFormValuesById, data 参数需传入数组');
			return;
		}
		data.map(item => {
			if (!item.formId) {
				return;
			}
			const formModuleId = __getFormModuleId(moduleId, item.formId);
			state = __setFormItemsProps(state, formModuleId, 'itemValue', item.values);
		});
		dispatch(state);
	}
});

function __setFormItemsProps(state, moduleId, key, values) {
	Object.keys(values).map(attrcode => {
		state = __setFormItemPropByCode(state, moduleId, attrcode, key, values[attrcode]);
	});
	return state;
}

function __setFormItemPropByCode(state, moduleId, attrcode, key, value) {
	return state.setIn([moduleId, 'formItems', attrcode, key], fromJS(value));
}

function __getFormItemsProps({ moduleId, formId, key, attrCodes }) {
	const formModuleId = __getFormModuleId(moduleId, formId);
	let returnObject = {};
	if (Array.isArray(attrCodes)) {
		attrCodes.map(attrcode => {
			returnObject[attrcode] = __getFormItemPropByCode({ moduleId, formModuleId: moduleId, attrcode, key });
		});
	} else if (typeof attrCodes === 'string') {
		returnObject[attrCodes] = __getFormItemPropByCode({
			moduleId,
			formModuleId,
			attrcode: attrCodes,
			key
		});
	}
	return returnObject;
}

function __immutableToJS(obj) {
	if (obj && typeof obj.toJS === 'function') {
		return obj.toJS();
	}
	return obj;
}

function __getFormItemPropByCode({ moduleId, formModuleId, attrcode, key }) {
	formModuleId = formModuleId || moduleId;
	const storeData = store.getState();
	const metaDataStored = storeData['meta'];
	const formDataStored = storeData[FORM_NAMESPACE];
	if (!metaDataStored && !formDataStored) {
		return null;
	}
	const metaModuleId = Array.isArray(moduleId) ? moduleId[0] : moduleId;
	const metaItemsStored = metaDataStored.getIn([metaModuleId, 'items']);
	let resultingValue;
	const metaKey = key === 'itemValue' ? 'initialvalue' : key;
	if (!metaItemsStored) {
		return null;
	}
	// 取所有属性
	if (!attrcode) {
		resultingValue = {};
		metaItemsStored.map(metaItem => {
			const attr = metaItem.get('attrcode');
			let byForm = formDataStored.getIn([formModuleId, 'formItems', attr, key]);
			const byMeta = metaItem.get(metaKey);
			const byTable = formDataStored.getIn([formModuleId, 'rowData', 'values', attr]);
			resultingValue[attr] = __formatItemValue({
				key,
				formValue: byForm,
				metaValue: byMeta,
				tableValue: byTable,
				itemtype: metaItem.get('itemtype')
			});
			if (key === 'itemValue') {
				resultingValue[attr].scale = metaItem.get('scale');
			}
		});
		return resultingValue;
	}

	let valueByFormItem = formDataStored.getIn([formModuleId, 'formItems', attrcode, key]);
	const valueByTableItem = formDataStored.getIn([formModuleId, 'rowData', 'values']);
	// 取指定 key 对应的值
	metaItemsStored.map(metaItem => {
		if (metaItem.get('attrcode') === attrcode) {
			resultingValue = __formatItemValue({
				key,
				formValue: valueByFormItem,
				metaValue: metaItem.get(metaKey),
				itemtype: metaItem.get('itemtype'),
				tableValue: valueByTableItem
			});
			if (key === 'itemValue') {
				resultingValue.scale = metaItem.get('scale');
			}
		}
	});
	return resultingValue;
}

/**
 * 属性值取值并格式化(itemValue.toString())
 * @param {String} key 属性名
 * @param {*} formValue form 中的 value
 * @param {*} metaValue meta 中的 value
 */
function __formatItemValue({ key, formValue, metaValue, itemtype, tableValue }) {
	let valueFormated;
	tableValue = __immutableToJS(tableValue);
	formValue = __immutableToJS(formValue);
	if (__formAttributeValid(formValue) && (key !== 'itemValue' || formValue.value !== undefined)) {
		valueFormated = formValue;
	} else if (__formAttributeValid(tableValue) && key === 'itemValue') {
		valueFormated = tableValue;
	} else if (metaValue !== undefined) {
		valueFormated = metaValue;
	} else {
		switch (key) {
			case 'itemValue':
				valueFormated = {
					value: null,
					display: null,
					scale: -1
				};
				break;
			case 'disabled':
			case 'required':
				valueFormated = false;
				break;
			case 'editable':
			case 'visible':
				valueFormated = true;
			// eslint-disable-next-line no-fallthrough
			default:
				break;
		}
	}
	if (key === 'itemValue') {
		valueFormated = __immutableToJS(valueFormated);
		switch (itemtype) {
			case 'switch':
			case 'switch_browse':
			case 'checkbox_switch':
				valueFormated.value = false;
				break;
			case 'numberstepper':
				valueFormated.value = 0;
				break;
			default:
				break;
		}
	}
	return valueFormated;
}

function __validateRequired(moduleIds) {
	if (typeof moduleIds === 'string') {
		moduleIds = [moduleIds];
	}
	let formValidate = true;
	const metaData = store.getState()['meta'];
	moduleIds.map(moduleId => {
		const metaItems = metaData.getIn([moduleId, 'items']);
		const requiredObj = __getFormItemPropByCode({ moduleId, key: 'required' });
		metaItems.map(metaItem => {
			const attrcode = metaItem.get('attrcode');
			const itemtype = metaItem.get('itemtype');
			const itemRequired = requiredObj[attrcode];
			if (itemRequired) {
				const itemValue = __getFormItemPropByCode({ moduleId, attrcode, key: 'itemValue' });
				if (itemtype !== 'refer') {
					if (typeof itemValue.value !== 'boolean' && !itemValue.value) {
						formValidate = false;
					}
				}
				// refer 单独处理
			}
		});
	});
	return formValidate;
}

function __getFormModuleId(moduleId, formId) {
	return formId ? [moduleId, formId].join(MODULE_SEPARATOR) : moduleId;
}

function __formAttributeValid(attr) {
	if (attr === undefined || attr == null || attr === '' || JSON.stringify(attr) === '{}') {
		return false;
	}
	return true;
}
function __getAttrcodesOfModule(storedMeta, moduleId) {
	const moduleItems = __immutableToJS(storedMeta.getIn([moduleId, 'items']));
	if (Array.isArray(moduleItems)) {
		return moduleItems.map(item => item.attrcode);
	}
	return [];
}
class Form extends Component {
	constructor(props) {
		super(props);
		this.state = {
			dialogVisible: false
		};

		// 相当于在构造函数中初始化this.state
		const { moduleId, config } = props;
		const { status = 'add', data, visibleField = 'visible' } = config;
		let backend_headid;
		if (status === 'add') {
			// add 时为后端逻辑的新增
			backend_headid = '2';
		} else {
			// 浏览, 编辑, 都视为后端逻辑的编辑
			backend_headid = '1';
		}
		let areaStatus = status;
		if (__formAttributeValid(data)) {
			if (__formAttributeValid(data.status)) {
				backend_headid = data.status;
			}
		}
		const initialValue = {
			formItems: {}, // form item info: value, disabled, editable ...
			tableRows: ImList(), // for multiple refer 展示多行数据
			areaStatus, // form 整单状态, 默认 browse/edit, 可调 api 设置 disabled, visible(todo)...
			visibleField,
			[FORM_MOBILE_CONSTS.BACKEND_STATUS]: backend_headid,
			rowData: fromJS(data) // for multiple refer 多选参照存储当前行数据
		};
		const formModuleId = __getFormModuleId(moduleId, config.formId);
		dispatch(getState().setIn([formModuleId], fromJS(initialValue)));
	}

	@executeTimesLogger('Form')
	render() {
		const { moduleId, config, metaItems } = this.props;
		const { containerClassName = 'nc-form-module-container' } = config;
		if (metaItems.size === 0) {
			return null;
		}
		this.config = this.config || {
			moduleId,
			...config
		};

		return (
			<List className={containerClassName}>
				{metaItems.map(metaItem => {
					return (
						<FormItem config={this.config} moduleId={moduleId} formId={config.formId} metaItem={metaItem} />
					);
				})}
			</List>
		);
	}
}

Form = connect(function() {
	return function(state, props) {
		const metaStored = state.meta || Map();
		const { moduleId } = props;
		return {
			metaItems: metaStored.getIn([moduleId, 'items']) || ImList()
		};
	};
})(Form);

/**
 * @description: 初始化表单函数
 * @param  {String} moduleId
 * @param  {Object} config
 * 		{
				// status: 'edit', // add/edit(待定/browse/disabled)
				// hasLabel: false,
				// hideDivider: true,
				// formId: '123d13edas',
				// data: {
					formId: '123d13edas',
					values: {
						name: { display: 1, value: 1 },
						age: { display: 2, value: 2 }
					}
				},
				// containerClassName: 'nc-form-module-container'
 * 		}
 */
export default function createForm(moduleId, config) {
	// 如果 store 中不存在改模板, 渲染为 null
	return <Form moduleId={moduleId} config={config} />;
}
