import React, { Component } from 'react';
import { Checkbox } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

import './style.less';

@extendsSmb(Checkbox)
class NCCheckbox extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Checkbox {...this.props} />;
	}
}
export default NCCheckbox;
