import { registerNameSpace } from '../store';

const { registerAction, dispatch } = registerNameSpace();

registerAction({
	renderItem(state, moduletype, moduleId, id, newItem) {
		dispatch(state.setIn(['renderItem', moduletype, moduleId, id], newItem));
	},
	getGlobalState(state, ...rest) {
		return state.getIn([].concat(rest));
	}
});
