import React, { Component } from 'react';
import './index.less';
import { Button } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Button)
class NCButton extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		let config = this.props;
		const { type = 'main', size } = this.props;
		if (size == 'small') {
			config = { ...config, inline: true };
		}
		return (
			<span className={`${type} button-container`}>
				<Button {...config} />
			</span>
		);
	}
}
export default NCButton;
