import React, { Component } from 'react';
import { Stepper } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Stepper)
class NCStepper extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Stepper {...this.props} />;
	}
}
export default NCStepper;
