import React, { Component } from 'react';
import './index.less';
import { Popover } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Popover)
class NCPopover extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Popover {...this.props} />;
	}
}
export default NCPopover;
