/* eslint-disable no-console */
import React, { Component } from 'react';
import { loadJS } from 'core';

export default class ReferLoader extends Component {
	constructor(props) {
		super(props);
		this.state = {
			refer: null
		};
	}

	componentDidMount() {
		this.createScript(this.props.refcode);
	}

	//WARNING! To be deprecated in React v17. Use new lifecycle static getDerivedStateFromProps instead.
	componentWillReceiveProps(nextProps) {
		if (this.props.refcode !== nextProps.refcode) {
			this.createScript(nextProps.refcode);
		}
	}

	refCodeToRefKey = refcode => {
		var s = refcode.split('/'),
			refKey;
		refKey = s.slice(s.length - 5).join('/');
		refKey.includes('.js') && (refKey = refKey.substring(0, refKey.length - 3));
		return refKey;
	};

	createScript = src => {
		this.setState(
			{
				refer: null
			},
			() => {
				if (src) {
					var that = this,
						refKey = this.refCodeToRefKey(src);
					loadJS('../../../../' + refKey + '.js', this.handleLoad.bind(null, refKey), () => {
						console.error(`找不到${src}这个文件，请检查引用路径`);
					});
				}
			}
		);
	};

	handleLoad = refKey => {
		let that = this;
		try {
			if (refKey === that.refCodeToRefKey(that.props.refcode)) {
				that.setState({
					refer: window[refKey].default
				});
			}
		} catch (e) {
			that.setState({
				refer: null
			});
			console.error(e.message);
			console.error(`请检查引用的${that.props.refcode}这个文件是源码还是编译好的。源码需要在config.json/buildEntryPath配相应的路径，编译好的则不用`);
		}
	};

	render() {
		let App = this.state.refer;
		return typeof App === 'function' && <App {...this.props} />;
	}
}
