import React, { Component } from 'react';
import { DatePicker } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(DatePicker)
class NCDatePicker extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <DatePicker {...this.props} />;
	}
}
export default NCDatePicker;
