import React, { Component } from 'react';
import { Result } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Result)
class NCResult extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Result {...this.props} />;
	}
}
export default NCResult;
