import React, { Component } from 'react';
import { LocaleProvider } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(LocaleProvider)
class NCLocaleProvider extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <LocaleProvider {...this.props} />;
	}
}
export default NCLocaleProvider;
