import React, { Component } from 'react';
import { TabBar } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(TabBar)
class NCTabBar extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <TabBar {...this.props} />;
	}
}
export default NCTabBar;
