
import React, { Component } from 'react';
import { Modal, List, Checkbox, Toast } from 'mobile/base';
import { SearchBar } from 'antd-mobile';
import { ajax } from 'mobile/api/ajax';
import BottomSelection from '../BottomFullSelection/bottomSelection'; //底部全选组件
import './searchModel.less';

export default class ReferLoader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            rowData: [],
            isNoData: false
            // voice: false
        };
    }

    //请求列表数据
    requestListData = (url, param) => {
        return new Promise(resolve => {
            ajax({
                url,
                data: param,
                success: (data) => {
                    resolve(data.data);
                },
            });
        });
    };

    componentWillReceiveProps(nextProps) {
        if (nextProps.isSearchModel) {
            this.setState({ rowData: [] }, () => { this.autoFocusInst.focus(); });
        }
        if (nextProps.isVoice) {
            Toast.success('嘿，siri ');
        }
    }

    contentChange = () => {}

    //取消事件关闭搜索model
    onCancel = () => {
        this.setState({ rowData: [], isNoData: false });
        this.props.onModelCancel();
    }

    voice = () => {
        Toast.success('嘿，siri !!!');
    }


    onChange = (val) => {
        const spanIcon = document.querySelector('.span-name-icon');
        spanIcon && val ? spanIcon.style.display  = 'none' : spanIcon.style.display  = 'block';
        if (!val) { this.setState({ rowData: [], isNoData: false }); return; }
        const reg = RegExp(val, 'g');
        const { refUrl, queryCondition, pageSize } = this.props;
        const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
        const param = { pid: '', keyword: val, pageInfo: { pageSize, pageIndex: 0 }, queryCondition: filterCondition };
        this.requestListData(refUrl, param).then(data => {
            if (data.rows.length) {
                const newDate = data.rows.map((item) => {
                    if (item.refname.match(reg)) {
                        item.refname = item.refname.replace(reg, `<span style='color: red'>${val}</span>`);
                    }
                    return item;
                });
                this.setState({ rowData: newDate, isNoData: true });
            } else {
                this.setState({ rowData: data.rows, isNoData: false });
            }

        });
    }

    //渲染搜索的数据区域
    generatorItem = (rowData) => {
        const { isMultiSelectedEnabled } = this.props;
        if (!isMultiSelectedEnabled) {
            return (
                <List>
                    {rowData.map((i, index) => (
                        <List.Item key={index} onChange={(e) => this.contentChange(i, e)}>
                            <span dangerouslySetInnerHTML={{ __html: i.refname }} />
                            <List.Item.Brief>{i.refcode}</List.Item.Brief>
                            <List.Item.Brief>{i.refpk}</List.Item.Brief>
                        </List.Item>
                    ))}
                </List>
            );
        } else {
            return (
                <List>
                    {rowData.map((i, index) => (
                        <Checkbox.CheckboxItem key={index} onChange={(e) => this.contentChange(i, e)}>
                            <span dangerouslySetInnerHTML={{ __html: i.refname }} />
                            <List.Item.Brief>{i.refcode}</List.Item.Brief>
                            <List.Item.Brief>{i.refpk}</List.Item.Brief>
                        </Checkbox.CheckboxItem>
                    ))}
                </List>
            );
        }
    }

    //底部展示规则
    visibleBottom = () => {
        const { isMultiSelectedEnabled } = this.props;
        if (isMultiSelectedEnabled) {
            return (
                <div className="name-footer">
                    <BottomSelection handleClick={this.handleClick}/>
                </div>
            );
        }
        return null;
    }

    render() {
        return (
            <div>
                <Modal className="search-model" visible={this.props.isSearchModel} transparent>
                    <div className="search-name-body">
                        <div className="search-name-header">
                            <SearchBar showCancelButton ref={ref => this.autoFocusInst = ref} placeholder={this.props.searchPlaceholder} onChange={this.onChange} onCancel={this.onCancel} />
                            <span className='span-name-icon' onClick={this.voice}><i className="icon iconfont iconyuyin" /></span>
                            {this.props.isMultistage ? <div className='search-name-div'>层级名称</div> : null}
                        </div>
                        {this.state.isNoData ?
                            <div className="search-name-content">{this.generatorItem(this.state.rowData)}</div> :
                            <p style={{ marginTop: '80px' }}>未搜索到相关信息</p>
                        }
                        {this.visibleBottom()}
                    </div>
                </Modal>
            </div>
        );
    }
}
