import React, { Component } from 'react';
import { SegmentedControl } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(SegmentedControl)
class NCSegmentedControl extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <SegmentedControl {...this.props} />;
	}
}
export default NCSegmentedControl;
