/* eslint-disable import/no-unresolved */
import React, { Component } from 'react';
import './style.less';
import { connect, actions, registerNameSpace, executeTimesLogger } from 'core';
import { List, Radio, Checkbox, Button, DatePicker } from 'mobile/base';
import { createForm } from 'mobile';
import { Map } from 'immutable';
import classnames from 'classnames';
import { isArray, isFunction, isDate } from 'mobile/utils';
import { timeFunctionTranslater, dateToEEA } from 'mobile/utils/timer';
import ReferLoader from '../../Refer/ReferLoader';
import { filtrateNameSpace } from '../constant';

const RadioItem = Radio.RadioItem;
const CheckboxItem = Checkbox.CheckboxItem;

const filinitalValue = Map({
  isDialogVisible: false
});

const { dispatch } = registerNameSpace(
  filtrateNameSpace,
  filinitalValue
);

function FiltrateFooter(props) {
  const { confirmEve, restEve } = props;

  const handleConfirm = () => {
    if (isFunction(confirmEve)) {
      confirmEve();
    }
  };

  const handleReset = () => {
    if (isFunction(restEve)) {
      restEve();
    }
  };

  return (
    <div className="filtrate-footer scale-1px">
      <div className="button-wraper bor-right">
        <Button onClick={handleReset}>重置</Button>
      </div>
      <div className="button-wraper">
        <Button onClick={handleConfirm}>确定</Button>
      </div>
    </div>
  );
}
class FiltrateList extends Component {
  constructor(props) {
    super(props);
    this.state = this.initialState(props);
    this.isRest = false;
  }

  componentWillReceiveProps(nextProps) {
    //   判断selectKey是否有变化，如果有那么就重新进行初始化
    if (this.props.selectKey !== nextProps.selectKey) {
      this.setState(this.initialState(nextProps));
    }
  }

  initialState = props => {
    const { dataSource, selectKey, moreLists } = props;
    let initState = {};

    if (selectKey == '') {
      return initState;
    }

    if (selectKey == 'more') return this.initMoreState(moreLists, dataSource);

    const {
      initialvalue,
      businessValue,
      isMultiSelectedEnabled,
      itemtype
    } = dataSource.get(selectKey);

    switch (itemtype) {
      case 'select':
        initState = this.initSelectedState(
          initialvalue,
          businessValue,
          isMultiSelectedEnabled
        );
        break;

      case 'refer':
        initState = this.initReferState();
        break;

      case 'rangepicker':
        initState = this.initRangepickerState(
          initialvalue,
          businessValue,
          dataSource,
          selectKey
        );
        break;
    }

    return initState;
  };

  //   初始化选择类型State
  initSelectedState = (initialvalue, businessValue, isMultiSelectedEnabled) => {
    let initialState = {
      selectedOptions: [],
      hasFiltrateFooter: false
    };

    if (businessValue) {
      initialState.selectedOptions = businessValue;
    } else if (initialvalue) {
      initialState.selectedOptions = [initialvalue];
    }

    if (isMultiSelectedEnabled) {
      initialState.hasFiltrateFooter = true;
    }

    return Object.assign(initialState, { isMultiSelectedEnabled });
  };

  // 初始化参照类型State
  initReferState = () => {
    let initialState = {
      referVisible: true
    };

    return initialState;
  };

  // 初始化更多展开列表数据
  initMoreState = () => {
    let initialState = {
      hasFiltrateFooter: true
    };

    return initialState;
  };

  // 初始化时间类型
  initRangepickerState = (initialvalue, businessValue, dataSource, selectKey) => {
    let hasFiltrateFooter = false;
    const { options = [] } = dataSource.get(selectKey);

    // 判断是否有自定义项
    if (options.length > 0) {
      options.map(option => {
        if (option.value === '#custom#') {
          hasFiltrateFooter = true;
        }
      });
    }

    return {
      ...this.initSelectedState(
        initialvalue,
        businessValue,
        hasFiltrateFooter
      ),
      firstvalue: '',
      secondvalue: ''
    };

  }

  //   单选点击事件 需要提供回调
  handleRadioSelectedValue = (option, itemtype) => {
    const { hasFiltrateFooter } = this.state;

    // 单选并且有底部，证明是时间类型，并且还是带有自定义类型的，所以下面的任何逻辑都不走
    if (hasFiltrateFooter) {
      this.setState({
        firstvalue: '',
        secondvalue: '',
        rangepickerDisplay: '',
        selectedOptions: [option]
      });
      return;
    }
    const { currentStore, selectKey, uniqueCode, onFiltrateChange } = this.props;

    const condition = {
      display: option.display || '',
      value: {
        firstvalue: option.value || ''
      }
    };
    let finalRes;
    currentStore.updateIn([uniqueCode, '$dataSource', selectKey], item => {
      item.businessValue = [option];
      item.condition = condition;
    });

    dispatch(currentStore);


    // 处理时间类型
    if (itemtype == 'rangepicker') {
      const { value } = condition;
      const rangepickerVal = JSON.parse(JSON.stringify(condition));
      for (let key in value) {
        const item = value[key];
        if (/^#.+#$/.test(item)) {
          rangepickerVal.value[key] = timeFunctionTranslater(item);
        }
      }
      finalRes = [{ field: selectKey, ...rangepickerVal }];
    } else {
      finalRes = [{ field: selectKey, ...condition }];
    }

    const { getAllconditions } = actions[filtrateNameSpace];
    getAllconditions(uniqueCode);


    if (isFunction(onFiltrateChange)) {
      onFiltrateChange(finalRes);
    }

    this.setState({
      selectedOptions: [option]
    });

    // 关闭遮罩
    this.onCancelDialog();
  };

  //   单选列表
  generatorRadioList = (dataSource, itemtype) => {
    const { options } = dataSource;
    let selectedVal = '';
    if (this.state.selectedOptions.length !== 0) {
      selectedVal = this.state.selectedOptions[0].value;

    }
    if (isArray(options)) {
      return options.map((opt) => {
        const { value, display } = opt;
        return (
          <RadioItem
            key={value}
            checked={selectedVal == value}
            onClick={() => {
              this.handleRadioSelectedValue(opt, itemtype);
            }}
          >
            {display}
          </RadioItem>
        );
      });
    }
  };

  // 多选点击事件
  /**
   * 业务逻辑：
   * 点击选择，操作state里面的数据 selectedOptions 这个属性
   * 当点击点击按钮的时候才会更新store里面的数据
   *
   */
  handleCheckboxSelectedValue = (option, e) => {
    let { selectedOptions } = this.state;

    const isSelected = e.target.checked;
    // 如果选中，更改state里面businessValue值如果取消就移除
    if (isSelected) {
      selectedOptions.push(option);
    } else {
      const copy = JSON.parse(JSON.stringify(selectedOptions));

      selectedOptions.map((item, index) => {
        if (item.value == option.value) {
          copy.splice(index, 1);
        }
      });
      selectedOptions = copy;
    }

    this.setState({ selectedOptions });
  };

  // 多选列表
  generatorCheckboxList = dataSource => {
    const { options } = dataSource;
    let { selectedOptions } = this.state;

    // 整理选中项
    const checkboxKeys = selectedOptions.map(item => item.value);

    if (isArray(options)) {
      return options.map((opt) => {
        const { value, display } = opt;
        return (
          <CheckboxItem
            key={value}
            onChange={e => {
              this.handleCheckboxSelectedValue(opt, e);
            }}
            checked={checkboxKeys.includes(opt.value)}
          >
            {display}
          </CheckboxItem>
        );
      });
    }
  };

  //  select类型渲染
  generatorSelectedList = (dataSource) => {
    /**
     * 处理逻辑：
     * 1、businessValue 为给业务组的值，也就是回填值，优先级最高
     * 2、initialvalue  初始值，也是回填值，优先级次之
     * 3、isMultiSelectedEnabled 为true的时候，有footer
     */

    const { isMultiSelectedEnabled } = dataSource;
    let listItems;

    if (isMultiSelectedEnabled) {
      listItems = this.generatorCheckboxList(dataSource);
    } else {
      listItems = this.generatorRadioList(dataSource);
    }

    return <List>{listItems}</List>;
  };

  // 渲染日期类型列表
  generatorRangePickerList = (dataSource, itemtype) => {
    const finalRes = this.generatorRadioList(dataSource, itemtype);
    // 删除自定义项
    let customIndex;
    let display = '';
    finalRes.map((item, index) => {
      const { key, props } = item;
      display = props.children;
      if (key == '#custom#') customIndex = index;
    });

    if (!isNaN(Number(customIndex))) {
      // 移除custorm项
      finalRes.splice(customIndex, 1);

      const generatorCustom = (
        <div
          id='custom-item'
          onClick={() => {
            this.setState({
              selectedOptions: []
            });
          }}
        >

          <DatePicker
            mode="date"
            extra='开始时间'
            value={this.state.firstvalue}
            onChange={time => {
              this.setState({
                firstvalue: time,
                rangepickerDisplay: display
              });
            }}
          >
            <List.Item></List.Item>
          </DatePicker>

          <span>-</span>

          <DatePicker
            mode="date"
            minDate={this.state.firstvalue}
            extra='结束时间'
            value={this.state.secondvalue}
            onChange={time => {
              this.setState({
                rangepickerDisplay: display,
                secondvalue: time
              });
            }}
          >
            <List.Item></List.Item>
          </DatePicker>

        </div>
      );

      finalRes.push(generatorCustom);
    }

    return finalRes;
  }

  // 渲染表单
  generatorFormList = () => {
    const { moduleId } = this.props;
    return createForm(moduleId, {
      visibleField: 'isForm'
    });
  };

  // 关闭参照
  closeReferDialog = () => {
    const { currentStore, uniqueCode } = this.props;
    dispatch(currentStore.updateIn([uniqueCode, 'isDialogVisible'], () => false));
  };

  // 参照onChange事件
  onMultiReferChange = (referValue, selectKey) => {
    const { currentStore, uniqueCode, onFiltrateChange } = this.props;
    let finalRes;
    let newReferValue;
    if (Object.prototype.toString.call(referValue) == '[object Object]') {
      newReferValue = [referValue];
    } else {
      newReferValue = referValue;
    }
    const condition = this.handleMultipleParams(newReferValue);
    currentStore.updateIn([uniqueCode, '$dataSource', selectKey], item => {
      item.businessValue = referValue;
      item.condition = condition;
    });

    dispatch(currentStore);


    finalRes = [{
      field: selectKey,
      ...condition
    }];
    if (isFunction(onFiltrateChange)) {
      onFiltrateChange(finalRes);
    }

    this.closeReferDialog();
  };

  // 生成参照列表
  generatorReferList = (dataSource, selectKey) => {
    // const {
    //   isMultistage = false,
    //   isMultiSelectedEnabled = false,
    //   label = '',
    //   refUrl = '',
    //   refName = ''
    // } = dataSource;
    return (
      <div>
        <ReferLoader
          {...dataSource}
          // isMultistage={isMultistage} //false为单级
          // isMultiSelectedEnabled={isMultiSelectedEnabled} //false为单选
          onChange={(referVal) => { this.onMultiReferChange(referVal, selectKey); }} // 单级单选当前行回调
          onCancel={this.closeReferDialog} //关闭回调
          // modelTitle={label} //title
          visible={this.props.isDialogVisible}
        // refUrl={refUrl} //参照查询url
        // refName={refName} //参照查询接口参数
        />
      </div>
    );
  };

  // 处理分发单页列表
  generatorListPage = (dataSource, selectKey) => {
    const getCurrentKeyData = dataSource.get(selectKey);

    const { itemtype } = getCurrentKeyData;

    switch (itemtype) {
      case 'select':
        return this.generatorSelectedList(getCurrentKeyData, selectKey);
      case 'refer':
        return this.generatorReferList(getCurrentKeyData, selectKey);
      case 'rangepicker':
        return this.generatorRangePickerList(getCurrentKeyData, itemtype, selectKey);
    }
  };

  //   分发渲染何种列表
  distributionList = (dataSource, selectKey, moreLists) => {
    if (selectKey == 'more') {
      return this.generatorFormList(dataSource, moreLists);
    }

    return this.generatorListPage(dataSource, selectKey);
  };

  // 处理脚部重置按钮
  handleReset = () => {
    this.isRest = true;
  };

  //处理重置逻辑
  HandleResetLogic = (selectKey, dataSource, currentStore, moreLists, uniqueCode) => {
    let finalRes;
    //如果是表单
    if (selectKey == 'more') {
      finalRes = moreLists.map(key => {
        currentStore.updateIn([uniqueCode, '$dataSource', key], item => {
          item.condition = null;
          item.businessValue = null;
        });
        return {
          field: key,
          display: '',
          value: {
            firstvalue: ''
          }
        };
      });
    } else {
      // 如果不是表单
      currentStore.updateIn([uniqueCode, '$dataSource', selectKey], item => {
        item.condition = null;
        item.businessValue = null;
      });

      finalRes = {
        field: selectKey,
        display: '',
        value: {
          firstvalue: ''
        }
      };

    }
    dispatch(currentStore);

    return finalRes;
  };

  // 处理脚部确定按钮
  handleConfirm = () => {
    const { selectKey, dataSource, currentStore, moreLists, uniqueCode, onFiltrateChange } = this.props;
    let finalRes;
    // 如果是重置，那要删除 businessValue 和 condition属性
    if (this.isRest) {
      // 处理重置逻辑
      finalRes = this.HandleResetLogic(selectKey, dataSource, currentStore, moreLists, uniqueCode);
      this.isRest = false;
    } else {
      if (selectKey == 'more') {
        // 处理表单提交
        const { moduleId } = this.props;
        const { getAllFormValue } = actions.form;
        const { rows } = getAllFormValue(moduleId)[moduleId];
        const onChangeRes = [];

        moreLists.map(key => {
          const { values } = rows[0];
          const formVal = values[key];
          const itemVal = {
            display: formVal.display || '',
            value: {
              firstvalue: formVal.value
            }
          };
          if (formVal.value) {
            delete formVal.value;
          }

          if (formVal.display) {
            delete formVal.display;
          }

          const finalItemParam = { ...itemVal, ...formVal };
          onChangeRes.push({ field: key, ...finalItemParam });

          currentStore.updateIn(
            [uniqueCode, '$dataSource', key],
            item => (item.condition = finalItemParam)
          );
        });
        finalRes = onChangeRes;
        dispatch(currentStore);

      } else {
        const currentTpl = dataSource.get(selectKey);
        let onChangeRes;

        switch (currentTpl.itemtype) {
          case 'select':
            onChangeRes = this.handleSelected(currentStore, selectKey, uniqueCode);
            break;
          case 'rangepicker':
            onChangeRes = this.hadleRangepickerCustom(currentStore, selectKey, uniqueCode);
            break;
        }

        finalRes = [onChangeRes];
      }
    }


    if (isFunction(onFiltrateChange)) {
      onFiltrateChange(finalRes);
    }
    const { getAllconditions } = actions[filtrateNameSpace];
    getAllconditions(uniqueCode);

    // 关闭遮罩
    this.onCancelDialog();
  };


  // 拼装多选参数
  handleMultipleParams = (parmsArray) => {

    const parms = {
      display: '',
      value: {
        firstvalue: ''
      }
    };

    if (isArray(parmsArray)) {
      parmsArray.map((item, index) => {
        if (index == parmsArray.length - 1) {
          parms.display += item.display;
          parms.value.firstvalue += item.value;
        } else {
          parms.display += item.display + ',';
          parms.value.firstvalue += item.value + ',';
        }
      });
    }

    return parms;

  }

  // 处理选择类型逻辑
  handleSelected = (currentStore, selectKey, uniqueCode) => {
    const { selectedOptions } = this.state;
    const condition = this.handleMultipleParams(selectedOptions);

    currentStore.updateIn([uniqueCode, '$dataSource', selectKey], item => {
      item.businessValue = selectedOptions;
      item.condition = condition;
    });

    dispatch(currentStore);
    return { field: selectKey, ...condition };

  };

  // 处理带有自定义时间类型
  hadleRangepickerCustom = (currentStore, selectKey, uniqueCode) => {
    let condition = {
      field: selectKey
    };
    const { selectedOptions, firstvalue, secondvalue, rangepickerDisplay } = this.state;
    if (selectedOptions.length !== 0) {
      condition = {
        condition,
        display: selectedOptions[0].display,
        value: {
          firstvalue: selectedOptions[0].value
        }
      };
    }

    if (firstvalue != '' || secondvalue != '') {

      condition = {
        condition,
        display: rangepickerDisplay,
        value: {
          firstvalue: '',
          secondvalue: ''
        }
      };

      if (isDate(firstvalue)) {
        condition.value.firstvalue = dateToEEA(firstvalue);
      }

      if (isDate(secondvalue)) {
        condition.value.secondvalue = dateToEEA(secondvalue);
      }

    }

    currentStore.updateIn([uniqueCode, '$dataSource', selectKey], item => {
      item.businessValue = selectedOptions;
      item.condition = condition;
    });

    dispatch(currentStore);

    return condition;

  }

  // 关闭遮罩
  onCancelDialog = () => {
    const { currentStore, uniqueCode } = this.props;
    dispatch(currentStore.updateIn([uniqueCode, 'isDialogVisible'], () => false));
    this.setState(this.initialState(this.props));
  };

  @executeTimesLogger('FiltrateList')
  render() {
    const {
      dataSource,
      tabLists,
      moreLists,
      selectKey
    } = this.props;



    if (selectKey == '') return null;
    const { hasFiltrateFooter } = this.state;

    return (
      <div className="filtrate-container">
        <div
          className={classnames('realy-container', {
            'has-footer': hasFiltrateFooter
          })}
        >
          {this.distributionList(dataSource, selectKey, moreLists, tabLists)}
        </div>
        {hasFiltrateFooter ? (
          <FiltrateFooter
            confirmEve={this.handleConfirm}
            restEve={this.handleReset}
          />
        ) : null}
      </div>
    );
  }
}

function mapStateToProps() {
  return function (state, props) {
    const { uniqueCode } = props;
    const currentStore = state[filtrateNameSpace];
    const dataSourceOfUniqueCode = currentStore.get(uniqueCode);
    if (!currentStore.get(uniqueCode)) {
      return {
        currentStore,
        dataSource: Map({}),
        tabLists: [],
        moreLists: []
      };
    }

    return {
      currentStore,
      dataSource: dataSourceOfUniqueCode.get('$dataSource'),
      tabLists: dataSourceOfUniqueCode.get('$tabLists'),
      moreLists: dataSourceOfUniqueCode.get('$moreLists'),
      isDialogVisible: dataSourceOfUniqueCode.get('isDialogVisible'),
    };
  };

}

FiltrateList.displayName = 'FiltrateList';

export default connect(mapStateToProps)(FiltrateList);
