import React, { Component } from 'react';
import { List } from 'mobile/base';
import { ajax } from 'mobile/api/ajax';
import './classSelect.less';

class ClassSelect extends Component {
    constructor(props) {
        super(props);
        this.state = { open: false, dataSource: [], approval: '' };
        this.propHeight = 0;
    }

    componentDidMount() {
        const { refTypeUrl } = this.props;
        ajax({
            url: refTypeUrl,
            data: {},
            success: (data) => {
                this.setState({ dataSource: data.data.rows });
            }
        });
    }

    onOpenChange = () => {
        this.setState({ open: !this.state.open });
    }

    listItem = () => {
        if (this.state.dataSource) {
            return this.state.dataSource.map((data, index) => {
                return (
                    <List.Item key={index} onClick={() => { this.clickList(data); }}>
                        <span>{data.refcode}</span>
                        <span style={{ paddingLeft: '15px' }}>{data.refname}</span>
                    </List.Item>
                );
            });
        }
    }

    //分类当前行点击事件
    clickList = (data) => {
        const { refUrl, refTypeUrl, classifiedCallback, queryCondition } = this.props;
        const approvalDate = data.refcode + data.refname;
        this.setState({ approval: approvalDate, open: !this.state.open });
        const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
        ajax({
            url: refTypeUrl,
            data: {
                pid: data.refpk,
                queryCondition: filterCondition,
            },
            success: (data) => {
                classifiedCallback(data.data.rows);
            }
        });
    }

    //分类标题
    classifiedHeadings = () => {
        return  this.state.approval ? this.state.approval : `${this.props.modelTitle}基本分类`;
    }

    //点击分类全部查询所有
    searchAll = () => {
        const { refUrl, queryCondition, classifiedCallback } = this.props;
        const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
        this.setState({ approval: `${this.props.modelTitle}基本分类` });
        ajax({
            url: refUrl,
            data: {
                queryCondition: filterCondition,
            },
            success: (data) => {
                classifiedCallback(data.data);
            }
        });
    }

    render() {
        return (
            <div>
                <div className='class-select' onClick={this.onOpenChange} ref={(select) => { //获取当前div的offsetTop
                    if (select) this.propHeight = select.offsetTop + 40;
                  }}
                >
                    <span className='my-span' style={{ marginLeft: '15px', color: this.state.approval ? '#ff7742' : 'black' }}>{this.classifiedHeadings()}</span>
                    <span className='my-span' style={{ marginLeft: '10px' }}>
                        {this.state.open ? <i className="icon iconfont iconshaixuanzhankai" /> : <i className="icon iconfont iconshaixuanshouqi"/>}
                    </span>
                </div>
                <div className='popup' onClick={this.onOpenChange} style={{
                    display: this.state.open ? 'block' : 'none',
                    top: this.propHeight,
                    height: document.documentElement.clientHeight - this.propHeight
                }}
                >
                    <div className='select-prop' style={{ top: this.propHeight, height: document.documentElement.clientHeight - 180 }}>
                        <List className='list-header' renderHeader={() => <span style={{ width: '100%', display: 'block' }} onClick={this.searchAll}>全部</span>}>{this.listItem()}</List>
                    </div>
                </div>

            </div>
        );
    }
}
export default ClassSelect;
