import { Toast } from 'mobile/base';

const pre = '/nccapi';
export function callAction(option) {
	const { data, url, success, error } = option;
	mtl.ncc.callAction({
		params: {
			busiParamJson: JSON.stringify(data)
		},
		url: pre + url,
		success: res => {
			console.log('res', res);
			if (res.code == 200) {
				success(res);
			} else {
				Toast.fail(res.message);
				error(res);
			}
		},
		fail: err => {
			console.log('err', err);
			error(err);
		}
	});
}
