import React from 'react';
import ReactDom from 'react-dom';
import { Provider } from 'react-redux';
// import { hashHistory } from 'react-router'
import {
  HashRouter as Router,
  withRouter,
  Switch,
  Route
} from 'react-router-dom';
import store from 'core/store';
import { JSBridgeInit } from 'mobile/api/jsapi';
import RouteWithSubRoutes from './subRoutes';


export default function RenderRouter(rootElement, htmlTagid) {
  JSBridgeInit(JSBridge => {
    ReactDom.render(
      <Provider store={store}>
        <Router>
            <Switch>
            {
              rootElement.map((route, i) => {
                   return <RouteWithSubRoutes key={i} {...route} />;
              })
            }
            </Switch>
        </Router>
      </Provider>,  
      document.getElementById(htmlTagid)
    );
  });
  

}



