import React, { Component } from 'react';
import { WingBlank } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(WingBlank)
class NCWingBlank extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <WingBlank {...this.props} />;
	}
}
export default NCWingBlank;
