import React, { Component } from 'react';
import { Pagination } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Pagination)
class NCPagination extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Pagination {...this.props} />;
	}
}
export default NCPagination;
