import React, { Component } from 'react';
import { Steps } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Steps)
class NCSteps extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Steps {...this.props} />;
	}
}
export default NCSteps;
