import React, { Component } from 'react';
import { Slider } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Slider)
class NCSlider extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Slider {...this.props} />;
	}
}
export default NCSlider;
