
import React, { Component } from 'react';
import { Modal, List, Switch } from 'mobile/base';
import { NavBar } from 'mobile/containers';
import { createForm } from 'rc-form';
import './setUpModel.less';

export default class ReferLoader extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        const navBarConfig = {
            goBack: {
                beforeEve: () => {
                    this.props.onCancel();
                    return true;
                }
            },
            title: '搜索设置',
        };
        return (
            <Modal className="set-model" visible={this.props.isSetUpModel} transparent>
                <NavBar {...navBarConfig} />
                <List>
                    <List.Item extra={<Switch checked={false} />}>包含下级</List.Item>
                    <List.Item extra={<Switch checked={false} />}>执行时包含下级</List.Item>
                    <List.Item extra={<Switch checked={false} />}>显示停用</List.Item>
                </List>
            </Modal>
        );
    }
}
