/* eslint-disable import/no-unresolved */
import { isFunction, getQueryString } from 'core/utils';
import { ajax } from 'mobile/api/ajax';

const getTmpUrl = '/nccloud/mob/platform/templet/querymobilepage';

export const lifecycle = function(pageCode) {
	const _lifecycle = function(App) {
		const requestMeta = App.prototype.requestMeta || defaultRequestMeta,
			requestData = App.prototype.requestData || defaultRequestData,
			init = App.prototype.init,
			old_componentWillMount = App.prototype.componentWillMount;

		App.prototype.componentWillMount = function() {
			let meta = isFunction(requestMeta) && requestMeta.call(this),
				data = isFunction(requestData) && requestData.call(this);

			isFunction(init) && init.call(this, meta, data, Promise.all([meta, data]));
			isFunction(old_componentWillMount) && old_componentWillMount.call(this);
		};
		function defaultRequestMeta() {
			return new Promise(resolve => {
				if (pageCode) {
					ajax({
						url: getTmpUrl,
						data: () => {
							let appcode = '';
							const loginInfoStr = sessionStorage.getItem('loginInfo');
							if (loginInfoStr) {
								const loginInfo = JSON.parse(loginInfoStr);
								appcode = loginInfo.appCode;
							} else {
								appcode = getQueryString('appcode');
								// const code = getQueryString('code');
								// console.log('appcode', appcode);
								// console.log('code', code);
							}

							return {
								pagecode: pageCode,
								appcode
							};
						},
						success(res) {
							resolve(res.data);
						}
					});
				} else {
					resolve(null);
				}
			});
		}

		function defaultRequestData() {
			console.log('defaultRequestData');
		}
	};

	return _lifecycle;
};
