import React, { Component } from 'react';
import { PickerView } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(PickerView)
class NCPickerView extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <PickerView {...this.props} />;
	}
}
export default NCPickerView;
