import React, { Component } from 'react';
import { Accordion } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Accordion)
class NCAccordion extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Accordion {...this.props} />;
	}
}
export default NCAccordion;
