import React, { Component } from 'react';
import { createSelector, connect, actions, registerNameSpace } from 'core';
import { List, ListView, Radio, Checkbox } from 'mobile/base';
import { NavBar } from 'mobile/containers';
import { Map, fromJS, setIn, getIn, deleteIn, is, update } from 'immutable';
import { isArray, isFunction } from 'mobile/utils';
import PropTypes from 'prop-types';
import { ajax } from 'mobile/api/ajax';
import { singleMutipleSelection } from '../../constant';
import './style.less';

const Item = List.Item;
const RadioItem = Radio.RadioItem;
const CheckboxItem = Checkbox.CheckboxItem;

//多级（树）使用单选中间级不可选组件
function RadioListEnd(props) {
  const { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve, selectedValue, SelectedValueEve } = props;
  // 点击跳转
  const handleJumpNextLevel = event => {
    jumpNextLevelEve(rowData, event);
  };
  const handleSelectedValue = event => {
    SelectedValueEve(refpk, rowData, event.target.checked);
  };
  let config = { multipleLine: true };
  //划分点击热区规则(1、不是末级就给整个item增加一个点击事件,整个事件是负责跳转)
  if (!isleaf) {
    return (<Item {...config} arrow="horizontal" onClick={handleJumpNextLevel}>{refname}</Item>);
  }
  return (
    <RadioItem key={refpk} checked={selectedValue === refpk} onClick={handleSelectedValue}>{refname}</RadioItem>
  );
}

//多级（树）使用单选中间级可选组件
function RadioListMiddle(props) {
  const { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve, selectedValue, SelectedValueEve } = props;
  // 点击跳转
  const handleJumpNextLevel = event => {
    jumpNextLevelEve(rowData, event);
  };
  const handleSelectedValue = event => {
    SelectedValueEve(refpk, rowData, event.target.checked);
  };
  //划分点击热区规则（1、是末级就给整个item增加一个点击事件,进行选中存值操作）
  //2、不是末级，给左侧和右侧增加点击事件，左侧点击选中，右侧跳转
  const extraContainer = (
    <div className="extra-cont" onClick={handleJumpNextLevel}>
        <div className="am-list-arrow am-list-arrow-horizontal" aria-hidden="true" />
    </div>
  );
  if (!isleaf) {
    return (
      <Item extra={extraContainer} className="refer-radio">
        <Radio
          className="my-radio"
          onClick={handleSelectedValue}
          checked={selectedValue == refpk}
        >
          {refname}
        </Radio>
      </Item>
    );
  }
  return (
    <Item className="refer-radio" onClick={handleSelectedValue}>
      <Radio className="my-radio" checked={selectedValue == refpk}>
        {refname}
      </Radio>
    </Item>
  );
}

//多级（树）使用多选中间级不可选
function CheckboxListEnd(props) {
  const { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve, selectedValue, SelectedValueEve } = props;
  // 点击跳转
  const handleJumpNextLevel = event => {
    jumpNextLevelEve(rowData, event);
  };
  let config = { multipleLine: true };
  const handleSelectedValue = event => {
    SelectedValueEve(refpk, rowData, event.target.checked);
  };

  if (!isleaf) {
    return (
      <Item
        {...config}
        arrow="horizontal"
        onClick={handleJumpNextLevel}
        className="refer-checkbox"
      >
        {refname}
      </Item>
    );
  }
  return (
    <CheckboxItem key={refpk} onChange={handleSelectedValue}>
      {refname}
    </CheckboxItem>
  );
}

//多级（树）使用多选中间级可选
function CheckboxListMiddle(props) {
  const { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve, selectedValue, SelectedValueEve } = props;
  // 点击跳转
  const handleJumpNextLevel = event => {
    jumpNextLevelEve(rowData, event);
  };
  let config = { multipleLine: true };
  const handleSelectedValue = event => {
    event.stopPropagation();
    SelectedValueEve(refpk, rowData, event.target.checked);
  };
  const arrow = (
    <div className="am-list-arrow am-list-arrow-horizontal" aria-hidden="true" />
  );
  const extraContainer = (
    <div className="extra-cont" onClick={handleJumpNextLevel}>{arrow}</div>
  );
  if (!isleaf) {
    return (
      <CheckboxItem key={refpk} onChange={handleSelectedValue} extra={extraContainer} className='refer-checkbox-container'>
      {refname}
      </CheckboxItem>
    );
  }
  return (
    <CheckboxItem key={refpk} onChange={handleSelectedValue}>{refname}</CheckboxItem>
  );
}

class GeneratorList extends Component {
  constructor(props) {
    super(props);
    const dataSource = new ListView.DataSource({ rowHasChanged: (row1, row2) => row1 !== row2 });
    this.state = {
      dataSource,
      isLoading: true,
      radioSelected: '', // 单选选中属性
      checkboxSelected: [],
      initialData: [],
      pageIndex: 0,
      totalPage: 0
    };
  }

  //请求列表数据
  requestListData = (url, param) => {
      return new Promise(resolve => {
          ajax({
              url,
              data: param,
              success: (data) => {
                  resolve(data.data);
              },
          });
      });
  };

  componentDidMount() {
      const { refUrl, queryCondition, pageSize, pid } = this.props;
      const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
      const param = { pid, pageInfo: { pageSize, pageIndex: this.state.pageIndex }, queryCondition: filterCondition };
      this.requestListData(refUrl, param).then(data => {
          this.setState({ totalPage: data.page ? data.page.totalPage : 0,
              isLoading: false,
              initialData: data.rows,
              dataSource: this.state.dataSource.cloneWithRows(data.rows) });
      });
  }

  //超出滚动加载
    onEndReached = () => {
        const { refUrl, pid, queryCondition, pageSize } = this.props;
        const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
        setTimeout(() => {
            this.setState({ pageIndex: this.state.pageIndex + 1, isLoading: true });
            if (this.state.pageIndex >= this.state.totalPage) {
                this.setState({ isLoading: false });
                return;
            };
            const param = { pid, pageInfo: { pageSize, pageIndex: this.state.pageIndex }, queryCondition: filterCondition };
            return this.requestListData(refUrl, param).then(data => {
                this.setState({ initialData: [...this.state.initialData, ...data.rows],
                    isLoading: false,
                    dataSource: this.state.dataSource.cloneWithRows([...this.state.initialData, ...data.rows]) });
            });
        });
    };

  // 选中跳转
  jumpNextLevel = (rowData) => {
      const { selectedList } = this.props;
      selectedList(rowData);
  }

  //单选选中事件
  handleRadioSelected = (refpk, rowData) => {
      this.props.onValueChange(rowData);
      this.setState({ radioSelected: refpk });
  };

  //多级单选参照 中间级可选 （选中内容存入store）
  handleCheckboxSelected = (refpk, rowData) => {
      const { setValue, deleteAll } = actions[singleMutipleSelection];
      this.setState({ radioSelected: refpk }, () => {
          deleteAll();
          setValue(rowData);
      });
  }

  handleMultistageCheckboxSelected = (refpk, rowData, isSelected) => {
    const { setValue, deleteValue } = actions[singleMutipleSelection];
        if (isSelected) {
            setValue(rowData);
        } else {
            deleteValue(rowData);
        }
  }


  // 多级（树）使用单选中间级不可选组件
  useRadioListEnd = rowData => {
    const { isleaf, refname, refpk, refcode } = rowData;
    const config = { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve: this.jumpNextLevel, selectedValue: this.state.radioSelected };
    return (
      <RadioListEnd {...config} SelectedValueEve={this.handleRadioSelected} />
    );
  }

  // 多级（树）使用单选中间级可选组件
  useRadioListMiddle = rowData => {
    const { isleaf, refname, refpk, refcode } = rowData;
    const config = { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve: this.jumpNextLevel, selectedValue: this.state.radioSelected };
    return (
      <RadioListMiddle {...config} SelectedValueEve={this.handleCheckboxSelected}/>
    );
  }

  // 多级（树）使用多选中间级不可选
  useCheckboxListEnd = rowData => {
    const { isleaf, refname, refpk, refcode } = rowData;
    const config = { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve: this.jumpNextLevel, selectedValue: this.state.checkboxSelected };
    return (
      <CheckboxListEnd {...config} SelectedValueEve={this.handleMultistageCheckboxSelected}/>
    );
  };

  // 多级（树）使用多选中间级可选
  useCheckboxListMiddle = rowData => {
    const { isleaf, refname, refpk, refcode } = rowData;
    const config = { rowData, isleaf, refname, refpk, refcode, jumpNextLevelEve: this.jumpNextLevel, selectedValue: this.state.checkboxSelected };
    return (
      <CheckboxListMiddle {...config} SelectedValueEve={this.handleMultistageCheckboxSelected}/>
    );
  }

  //根据条件渲染中间列表数据
  generatorItem = rowData => {
    const { isMultiSelectedEnabled, isRadioListMiddle, isCheckboxListMiddle } = this.props;
    //首先判断是单选还是多选
    if (isMultiSelectedEnabled) {
      if (isCheckboxListMiddle) {
        return this.useCheckboxListMiddle(rowData); //多选中间级可选
      }
      return this.useCheckboxListEnd(rowData); //多选中间级不可选
    } else {
      if (isRadioListMiddle) {
          return this.useRadioListMiddle(rowData); //单选中间级可选
      }
      return this.useRadioListEnd(rowData); //单选中间级不可选
    }
  }

  render() {
    return (
      <ListView
        ref={el => (this.lv = el)}
        dataSource={this.state.dataSource}
        renderFooter={() => (
          <div style={{ padding: 30, textAlign: 'center' }}>
            {this.state.isLoading ? 'Loading...' : 'Loaded'}
          </div>
        )}
        renderRow={this.generatorItem}
        pageSize={4}
        style={{ height: document.documentElement.clientHeight - 110 }}
        scrollRenderAheadDistance={500}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={10}
      />
    );
  }
}

GeneratorList.propTypes = {
  onlyLeafCanSelect: PropTypes.bool,  // 是否只有树节点可选
};

GeneratorList.defaultProps = {
  onlyLeafCanSelect: false, // 是否只有树节点可选
};

function mapStateToProps() {
  return function(state, props) {
    return {};
  };
}

export default connect(mapStateToProps)(GeneratorList);
