import React, { Component } from 'react';
import { Tabs } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Tabs)
class NCTabs extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Tabs {...this.props} />;
	}
}
export default NCTabs;
