import React, { Component } from 'react';
import { List } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';
import './style.less';

@extendsSmb(List)
class NCList extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <List {...this.props} />;
	}
}
export default NCList;
