import React, { Component } from 'react';
import { Drawer } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Drawer)
class NCDrawer extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Drawer {...this.props} />;
	}
}
export default NCDrawer;
