import React, { Component } from 'react';
import { TextareaItem } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(TextareaItem)
class NCTextareaItem extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <TextareaItem {...this.props} />;
	}
}
export default NCTextareaItem;
