import React, { Component } from 'react';
import { Carousel } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Carousel)
class NCCarousel extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Carousel {...this.props} />;
	}
}
export default NCCarousel;
