import React, { Component } from 'react';
import { createSelector, connect, actions, registerNameSpace } from 'core';
import { TabBar } from 'mobile/base';
import { OrderedMap, fromJS} from 'immutable';
import { multileLeveNameSpace } from '../constant';
import GeneratorList from './GeneratorList';
import './style.less';

const initalValue = fromJS({ dataSource: OrderedMap({}) });
const { registerAction, dispatch, getState } = registerNameSpace(multileLeveNameSpace, initalValue);

/**
 * 组件会传一个初始位置，也就是对应的tabBar的key,并且把这个key注册进入store内
 * @class MultilevelRefer
 * @extends {Component}
 */
class MultilevelRefer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: '',
      tabBarKey: OrderedMap().set(this.props.rootHeadings.referPk, {  //初始化导航标题
        refpk: this.props.rootHeadings.referPk,
        title: this.props.rootHeadings.title,
      })
    };
  }

  // 当选中之后获取refpk值，用这个值注册空间 key:{},并且更新tabBarKey
  selectedListCallBack = list => {
    const { tabBarKey } = this.state;
    const { dataSource } = this.props;
    const { refpk, refname, pid } = list;
    let newTabBarKey;
    // 如果dataSource里面没有这个 refpk的键，就把这个键存进键栈 并且更新视图
    if (!tabBarKey.has(refpk)) {
      newTabBarKey = tabBarKey.set(refpk, {
        refpk,
        title: list.refname,
        rowData: list,
        pid
      });
      this.setState({ tabBarKey: newTabBarKey });
    }
    this.setState({ selectedTab: refpk });
  };


  //单选选中之后触发父组件回调
  handleOnValueChange = (rowData) => {
    this.props.handleClick(rowData);
  };

  // 处理点击tabBar逻辑 选中之后对tabBar数据进行筛选
  handleClickTabBar = (key, list) => {
    const { tabBarKey } = this.state;
    let tabs = OrderedMap();
    tabs = tabs.set(key, list);
    function getRoute(list) {
      if (!list.pid) return null;
      tabBarKey.map((tab, index) => {
        if (list.pid === tab.refpk) {
          tabs = tabs.set(tab.refpk, tab);
          getRoute(tab);
        }
      });
    }
    getRoute(list);
    this.setState({
      selectedTab: key,
      tabBarKey: tabs
    });
  };

  renderTabItem = () => {
    const { tabBarKey } = this.state;
    const tabItems = [];
    tabBarKey.map((data, refpk) => {
      tabItems.push(
        <TabBar.Item
          title={(
            <div style={{ display: 'flex' }}>
                          <i style={{ marginTop: '1px', fontSize: '12px', display: tabItems.length ? 'block' : 'none' }} className='iconfont iconshaixuanyoucejiantou' />
                          <span>{data.title}</span>
            </div>
            )}
          key={data.refpk}
          selected={this.state.selectedTab === refpk}
          onPress={() => { this.handleClickTabBar(refpk, data); }}
          data-seed="logId"
        >
          <GeneratorList
            pid={refpk}
            selectedList={this.selectedListCallBack}
            onValueChange={this.handleOnValueChange}
            {...this.props}
          />
        </TabBar.Item>
      );
    });
    return tabItems;
  };

  render() {
    return (
      <div style={{ width: '100%' }} className='refer-tab-bar-container'> 
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#ff7742"
          barTintColor="#f5f5f9"
          tabBarPosition="top"
          animated={true}
        >
          {this.renderTabItem()}
        </TabBar>
      </div>
    );
  }
}

function mapStateToProps() {
  return function(state, props) {
    const currentState = state[multileLeveNameSpace];
    return {
      dataSource: currentState.get('dataSource')
    };
  };
}

export default connect(mapStateToProps)(MultilevelRefer);
