import React, { Component } from 'react';
import './style.less';
// eslint-disable-next-line import/no-unresolved
import { connect, registerNameSpace, executeTimesLogger } from 'core';
import { Map, List as ImmList, is } from 'immutable';
import FiltrateWraper from './FiltrateWraper';
import FiltrateList from './FiltrateList';
import { filtrateNameSpace } from './constant';
import actionsForApi from './methods';

// tabs包含的类型
const tabItemType = ['select', 'refer', 'rangepicker'];

const filinitalValue = Map({
  isDialogVisible: false
});

const { registerAction, dispatch } = registerNameSpace(
  filtrateNameSpace,
  filinitalValue
);

registerAction(actionsForApi);

class Filtrate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectKey: ''
    };

    const { dataSource, uniqueCode } = this.props;

    if (!uniqueCode) {
      // eslint-disable-next-line no-console
      throw new Error('uniqueCode is required');
    }

    if (dataSource.size > 0) {
      this.initDataSource(dataSource, uniqueCode);
    }

  }

  componentWillReceiveProps(nextProps) {
    const { dataSource } = this.props;
    if (!is(dataSource, nextProps.dataSource)) {
      this.initDataSource(nextProps.dataSource, nextProps.uniqueCode);
    }
  }

  /**
   *initDataSource 初始化业务组传过来的数据
   *
   * @params propsDataSource 业务组传过来的数据，可能是模板（对象），也可能是一个拼装好的数组
   * @return _dataSource Map结构 attrcode为key
   * @return tabLists 左侧包含的数据 moreLists隐藏包含的数据
   */
  initDataSource = (propsDataSource, uniqueCode) => {
    const _dataSource = Map({});
    const _tabLists = [];
    const _moreLists = [];
    let { currentStore } = this.props;
    let res;
    // 输出一个Map结构，key是attrCode
    // 这个最后是要变的，判断是否为数组
    if (ImmList.isList(propsDataSource)) {
      // 变成orderMap结构
      propsDataSource.map(item => {
        const attrcode = item.get('attrcode');
        const itemtype = item.get('itemtype');
        const isForm = item.get('isForm');

        if (!isForm && tabItemType.includes(itemtype) && _tabLists.length < 4) {
          _tabLists.push(attrcode);
        } else {
          _moreLists.push(attrcode);
        }

        if (!res) {
          res = _dataSource.set(attrcode, item);
        } else {
          res = res.set(attrcode, item);
        }
      });

      if (_moreLists.length > 0) {
        _tabLists.push('more');
      }
    }

    res = Map(res.toJS());

    currentStore = currentStore
      .set('uniqueCode', uniqueCode)
      .setIn([uniqueCode, '$dataSource'], res)
      .setIn([uniqueCode, '$tabLists'], _tabLists)
      .setIn([uniqueCode, '$moreLists'], _moreLists);

    dispatch(currentStore);
  };

  // 处理选中逻辑
  handleOnSelected = (key, selected) => {
    const { $dataSource } = this.props;
    if (!$dataSource.get(key)) {
      this.setState({
        selectKey: 'more'
      });

      return;
    }

    const updataState = {
      selectKey: ''
    };
    if (selected) {
      updataState.selectKey = key;
    } else {
      updataState.selectKey = '';
    }

    this.setState(updataState);
  };


  @executeTimesLogger('Filtrate')
  render() {
    const { dataSource, moduleId, onFiltrateChange, $moreLists, uniqueCode } = this.props;
    if (!dataSource && dataSource.size == 0) return null;

    return (
      <div className="filtrate-container">
        <FiltrateWraper
          uniqueCode={uniqueCode}
          onSelectedEve={this.handleOnSelected}
          hasUnfoldIcon={!($moreLists.length === 0)}
        >
          {this.state.selectKey !== '' ? (
            <FiltrateList
              uniqueCode={uniqueCode}
              selectKey={this.state.selectKey}
              moduleId={moduleId}
              onFiltrateChange={onFiltrateChange}
            />
          ) : null}
        </FiltrateWraper>
      </div>
    );
  }
}

Filtrate.propTypes = {};

Filtrate.defaultProps = {};

function mapStateToProps() {
  return function (state, props) {
    const currentStore = state[filtrateNameSpace];
    const { moduleId, uniqueCode } = props;
    const dataSourceOfUniqueCode = currentStore.get(uniqueCode);
    const dataSource = state.meta.getIn([moduleId, 'items']) || ImmList();

    const baseConfig = {
      globleState: state,
      moduleId,
      dataSource,
      currentStore
    };

    if (!dataSourceOfUniqueCode) {
      return {
        ...baseConfig,
        $dataSource: Map({}),
        $tabLists: [],
        $moreLists: []
      };
    }

    return {
      ...baseConfig,
      $dataSource: dataSourceOfUniqueCode.get('$dataSource'),
      $tabLists: dataSourceOfUniqueCode.get('$tabLists'),
      $moreLists: dataSourceOfUniqueCode.get('$moreLists')
    };
  };
}
Filtrate.displayName = 'Filtrate';
Filtrate = connect(mapStateToProps)(Filtrate);
export { Filtrate };
