import React, { Component } from 'react';
import { Radio } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Radio)
class NCRadio extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<span className="nc-radio">
				<Radio {...this.props} />
			</span>
		);
	}
}
export default NCRadio;
