import React, { Component } from 'react';
import { Modal } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Modal)
class NCModal extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Modal {...this.props} />;
	}
}

export default NCModal;
