import React, { Component } from 'react';
import { Tag } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Tag)
class NCTag extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Tag {...this.props} />;
	}
}
export default NCTag;
