import React, { Component } from 'react';
import { ListView } from 'antd-mobile';
import { PullToRefresh, List } from '..';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(ListView)
class NCListView extends Component {
	constructor(props) {
		super(props);
		this.state = {
			refreshing: false, // 下拉刷新
			loading: false, // 上拉加载
			height: props.containerHeight || document.documentElement.clientHeight + 'px' // 容器高度
			// height: props.containerHeight || document.documentElement.clientHeight - 50 + 'px'
		};
	}

	onRefreshHandler = () => {
		const { loading, refreshing } = this.state;
		if (loading || refreshing) {
			return false;
		}
		console.log('list refreshing');
		const { onRefresh } = this.props;
		this.setState({ refreshing: true });
		if (typeof onRefresh === 'function') {
			const result = onRefresh();
			if (result instanceof Promise) {
				result
					.catch(err => console.log('onRefresh 发生错误', err))
					.finally(() => this.setState({ refreshing: false }));
			} else {
				this.setState({ refreshing: false });
			}
		} else {
			setTimeout(() => {
				this.setState({ refreshing: false });
			}, 1000);
		}
	};

	onEndReachedHandler = () => {
		const { loading, refreshing } = this.state;
		// console.log('onEndReachedHandler');
		if (loading || refreshing) {
			return false;
		}
		// console.log('loading more');
		const { onEndReached } = this.props;
		this.setState({ loading: true });
		if (typeof onEndReached === 'function') {
			const result = onEndReached();
			if (result instanceof Promise) {
				result
					.catch(err => console.log('onEndReached 发生错误', err))
					.finally(() => this.setState({ loading: false }));
			} else {
				this.setState({ loading: false });
			}
		} else {
			setTimeout(() => {
				this.setState({ loading: false });
			}, 1000);
		}
	};

	render() {
		const { render, onRefresh, onEndReached, renderFooter, style, ...propsToPass } = this.props;
		const { height, refreshing, loading } = this.state;
		// refresh
		if (typeof onRefresh === 'function' || onRefresh instanceof Promise) {
			propsToPass.pullToRefresh = (
				<PullToRefresh
					direction='down'
					indicator={{ deactivate: 'refreshing...' }}
					refreshing={refreshing}
					onRefresh={this.onRefreshHandler}
				/>
			);
		}
		// end reach footer
		if (typeof onEndReached === 'function') {
			propsToPass.onEndReached = this.onEndReachedHandler;
			if (!renderFooter) {
				propsToPass.renderFooter = () => <p style={{ textAlign: 'center' }}>{loading ? 'loading...' : '我是有底线的...'}</p>;
			}
		}
		return (
			<ListView
				ref={el => (this.lv = el)}
				// renderRow={row => this.renderRow(row)}
				style={{
					height,
					...style
				}}
                renderFooter={renderFooter}
				scrollRenderAheadDistance={200}
				scrollEventThrottle={20}
				onEndReachedThreshold={10}
				{...propsToPass}
			/>
		);
	}
}
export default NCListView;
