/**
 *继承静态成员
 */
export default compoent => nc_comt => {

  const compoentKeysSet = new Set(Object.keys(compoent));

  if (compoentKeysSet.size > 0) {
    if (compoentKeysSet.has("defaultProps"))
      compoentKeysSet.delete("defaultProps");
      for(let staticMember of compoentKeysSet){
          nc_comt[staticMember] = compoent[staticMember];
      }
  }

  return nc_comt;
};
