import React, { Component } from 'react';
import { createSelector, connect, actions, registerNameSpace } from 'core';
import { ListView, List, Checkbox } from 'mobile/base';
import { fromJS } from 'immutable';
import { ajax } from 'mobile/api/ajax';
import './single.less';

class Single extends Component {
    constructor(props) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2
        });
        this.state = {
            dataSource,
            isLoading: true,
            initialData: [],
            pageIndex: 0,
            totalPage: 0
        };
    }

    //请求列表数据
    requestListData = (url, param) => {
        return new Promise(resolve => {
            ajax({
                url,
                data: param,
                success: (data) => {
                    resolve(data.data);
                },
            });
        });
    };

     //滚动加载到底部时候加载
    onEndReached = () => {
        const { refUrl, refName, queryCondition, pageSize } = this.props;
        const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
        setTimeout(() => {
            this.setState({ pageIndex: this.state.pageIndex + 1, isLoading: true });
            if (this.state.pageIndex >= this.state.totalPage) {
                this.setState({ isLoading: false });
                return;
            };
            const param = { pid: refName, pageInfo: { pageSize, pageIndex: this.state.pageIndex }, queryCondition: filterCondition };
            return this.requestListData(refUrl, param).then(data => {
                this.setState({ initialData: [...this.state.initialData, ...data.rows],
                    isLoading: false,
                    dataSource: this.state.dataSource.cloneWithRows([...this.state.initialData, ...data.rows]) });
            });
        });
    };

    //第二次传进来的classifiedDataSource 不同时候 应该取新传入的
    componentWillReceiveProps(nextProps) {
        if (this.props.classifiedDataSource !== nextProps.classifiedDataSource) {
            this.setState({ dataSource: this.state.dataSource.cloneWithRows(nextProps.classifiedDataSource) });
        }
    }

    componentDidMount() {
        const { refUrl, refName, queryCondition, pageSize } = this.props;
        const filterCondition  = queryCondition && typeof queryCondition === 'function' ? queryCondition() : {};
        const param = { pid: refName, pageInfo: { pageSize, pageIndex: this.state.pageIndex }, queryCondition: filterCondition };
        this.requestListData(refUrl, param).then(data => {
            this.setState({ totalPage: data.page ? data.page.totalPage : 0,
                initialData: data.rows,
                isLoading: false,
                dataSource: this.state.dataSource.cloneWithRows(data.rows) });
        });
    }

    //渲染单条行的数据
    generatorItem = rowData => {
        const { rowEditable, renderRefRow, isMultiSelectedEnabled, isStyleUpAndDown, isStyleLeftAndRight } = this.props;
        if (rowEditable && typeof renderRefRow === 'function') {
            return renderRefRow({ rowData });
        } else {
            if (!isMultiSelectedEnabled) {
                 return (
                    <List.Item multipleLine extra={isStyleLeftAndRight ? rowData.refcode : ''} onClick={() => { this.contentClick(rowData); }}>
                          {rowData.refname}
                          {isStyleUpAndDown ? <List.Item.Brief>{rowData.refcode}</List.Item.Brief> : null}
                    </List.Item>
                 );
            } else {
                 return (
                    <Checkbox.CheckboxItem extra={isStyleLeftAndRight ? rowData.refcode : ''} onChange={(e) => { this.contentChange(rowData, e); }}>
                          {rowData.refname}
                          {isStyleUpAndDown ? <List.Item.Brief>{rowData.refcode}</List.Item.Brief> : null}
                    </Checkbox.CheckboxItem>
                 );
            }
        }
    }

    //单级单选点击获取单行数据
    contentClick = (content) => {
        this.props.handleClick(content);
    }

    //单级多选点击获取数据存入store
    contentChange = (content, e) => {
        const { setValue, deleteValue } = actions.SINGLE_MULTIPLE_SELECTION;  //获取当前命名空间action方法
        if (e.target.checked) setValue(content);  //判断是否选中
        else deleteValue(content);
    }

    render() {
        return (
            <div style={{ width: '100%' }}>
                <ListView
                    ref={el => (this.lv = el)}
                    dataSource={this.state.dataSource}
                    renderRow={this.generatorItem}
                    pageSize={this.props.pageSize}
                    style={{ height: document.documentElement.clientHeight - 110, overflow: 'auto' }}
                    scrollRenderAheadDistance={500}
                    onEndReached={this.onEndReached}
                    onEndReachedThreshold={10}
                    renderFooter={() => <p style={{ textAlign: 'center' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</p>}
                />
            </div>
        );
    }
}

function mapStateToProps() {
    return function(state, props) {
        return {

        };
    };
}

export default connect(mapStateToProps)(Single);
