import React, { Component } from 'react';
import { InputItem } from 'mobile/base';
import PropTypes from 'prop-types';
import { ncRounding, commafy, removeThousands } from 'mobile/utils';
import './style.less';

class InputNumber extends Component {
    constructor(props) {
        super(props);
        this.state = this.initState(props);
    }

    initState = (props) => {
        const state = {
            value: props.value ? this.formatValue(String(props.value), Number(props.scale)) : '',
            verify: props.verify,
            reg: new RegExp('^(\\-)?[0-9]*(\\.[0-9]{0,' + props.scale + '})?$'),
            scale: props.scale ? Number(props.scale) : 0
        };

        return state;
    }

    componentWillReceiveProps(nextProps) {
        if (this.compareThis2NextOfProps(this.props, nextProps)) {
            this.setState(this.initState(nextProps));
        }
    }

    /**
     *对比props 和 nextProps 属性值
     *
     * @memberof InputNumber
     */
    compareThis2NextOfProps = (props, nextProps) => {
        const stateKeys = Object.keys(this.state);

        for (let i = 0; i < stateKeys.length; i++) {
            const key = stateKeys[i];
            if (typeof props[key] != 'object') {
                if (props[key] != nextProps[key]) return true;
            }
            return false;
        }

    }


    /**
     * @param {*} val   value值
     * @param {*} max   最大长度值
     * @param {*} scale 精度
     * @returns true 超长 false 未超长
     * @memberof InputNumber
     */
    checkMaxLength = (val, max, scale) => {
        const newMax = Number(max);
        const newVal = String(val);
        if (!isNaN(newMax)) {
            return newMax < ncRounding(newVal, scale).length;
        }
        return false;
    }

    /**
     *
     *@param {*} val 
     * @memberof InputNumber
     */
    handleOnChange = (val) => {
        const { reg, scale } = this.state;
        const { maxLength } = this.props;

        //精度超出范围提示
        // eslint-disable-next-line no-unused-vars
        const [beforePoint, afterPoint] = val.split('.');
        if (afterPoint && afterPoint.length > scale) {
            console.warn('Scale out of range');
            return null;
        }

        // 判断输入值是否合法
        if (!reg.test(val)) return null;

        // 如果精度为0 就不允许输入小数点了
        if (!scale && val.includes('.')) return false;

        // 超长给出提示
        if (this.checkMaxLength(val, maxLength, scale)) {
            console.warn('The input value is too long');
            return null;
        };

        this.setState({ value: val });
    }


    /**
     *格式化数据格式
     *
     * @memberof InputNumber
     */
    formatValue = (val, scale) => {
        return commafy(ncRounding(val, scale));
    }


    /**
     *失去焦点
     *
     * @memberof InputNumber
     */
    handleOnBlur = (val) => {
        const { scale } = this.state;
        const finalVal = this.formatValue(val, scale);
        this.setState({
            value: finalVal
        });
    }

    handleOnFocus = (val) => {
        const finalVal = removeThousands(val);
        this.setState({
            value: finalVal
        });
    }

    render() {
        return (
            <div className='input-container'>
                <span className='icon-symbol'>$</span>
                <InputItem
                    className='input-num'
                    onChange={this.handleOnChange}
                    onFocus={this.handleOnFocus}
                    onBlur={this.handleOnBlur}
                    value={this.state.value}
                />
            </div>
        );
    }
}

const propTypes = {
    min: PropTypes.number,
    max: PropTypes.number,
    onBlur: PropTypes.func,
    onChange: PropTypes.func,
    maxLength: PropTypes.number
};

const defaultProps = {
    verify: true,
    scale: 0,
    disabled: false,
    value: ''
};

InputNumber.propTypes = propTypes;
InputNumber.defaultProps = defaultProps;
InputNumber.displayName = 'InputNumber';

export default InputNumber;
