import React, { Component } from 'react';
import { PullToRefresh } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(PullToRefresh)
class NCPullToRefresh extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <PullToRefresh {...this.props} />;
	}
}
export default NCPullToRefresh;
