import React, { Component } from 'react';
// eslint-disable-next-line import/no-unresolved
import { connect, registerNameSpace, executeTimesLogger } from 'core';
import classnames from 'classnames';
import {
  Map
} from 'immutable';
import { filtrateNameSpace } from '../constant';

import './style.less';

const filinitalValue = Map({
  isDialogVisible: false
});

const { dispatch } = registerNameSpace(
  filtrateNameSpace,
  filinitalValue
);

class FiltrateWraper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      selected: ''
    };
    this.propHeight = 0;
  }

  componentWillReceiveProps(nextProps) {
    if (
      this.props.isDialogVisible !== nextProps.isDialogVisible &&
      nextProps.isDialogVisible == false
    ) {
      this.setState({
        selected: ''
      });
    }

  }

  onOpenChange = (e) => {
    if (e.target.id == 'filtrate-mask') {
      const { currentStore, uniqueCode } = this.props;
      dispatch(currentStore.updateIn([uniqueCode, 'isDialogVisible'], () => false));
    }

  };

  calculateoffsetTop = select => {
    //获取当前div的offsetTop
    if (select) this.propHeight = select.offsetTop + 40;
  };

  // 处理选中函数
  onSelectedTab = key => {
    const { selected } = this.state;
    const { onSelectedEve, currentStore, uniqueCode } = this.props;

    if (selected === key) {
      // 取消选中
      onSelectedEve(key, false);
      dispatch(currentStore.updateIn([uniqueCode, 'isDialogVisible'], () => false));
      this.setState({
        selected: ''
      });
    } else {
      // 选中
      onSelectedEve(key, true);
      dispatch(currentStore.updateIn([uniqueCode, 'isDialogVisible'], () => true));
      this.setState({
        selected: key
      });
    }
  };

  handleTabs = (
    dataSource,
    moreLists,
    tabLists) => {
    if (dataSource.size == 0) return null;
    const tabWidth = { maxWidth: 100 / tabLists.length + '%' };

    return tabLists.map((tabAttrcode) => {
      if (tabAttrcode === 'more') {
        return (
          <div
            className={classnames('filtrate-tab', {
              selected: this.state.selected == tabAttrcode
            })}
            style={tabWidth}
          >
            <i
              className="icon iconfont iconzhankaixiangqing more"
              onClick={this.onSelectedTab.bind(this, tabAttrcode)}
            />
          </div>
        );
      }

      const { label, isBackfill, condition } = dataSource.get(tabAttrcode);
      let tabTitle;
      if (isBackfill && condition) {
        const { display } = condition;
        tabTitle = display;
      } else {
        tabTitle = label;
      }

      return (
        <div
          className={classnames('filtrate-tab', {
            selected: this.state.selected == tabAttrcode
          })}
          style={tabWidth}
        >
          <div className="tab-title">{tabTitle}</div>
          <span className="">
            {this.state.selected == tabAttrcode ? (
              <i
                className="icon iconfont iconshaixuanzhankai"
                onClick={this.onSelectedTab.bind(this, tabAttrcode)}
              />
            ) : (
                <i
                  className="icon iconfont iconshaixuanshouqi"
                  onClick={this.onSelectedTab.bind(this, tabAttrcode)}
                />
              )}
          </span>
        </div>
      );
    });

  };

  generatorMaskContainer = isDialogVisible => {
    //   遮罩高度
    const maskHeight = document.documentElement.clientHeight - this.propHeight;

    const containerBottom = 95;

    const containerHeight = maskHeight - containerBottom;
    return (
      <div>
        {/* 遮罩层 */}
        <div
          className="popup"
          id="filtrate-mask"
          style={{
            display: isDialogVisible ? 'block' : 'none',
            height: maskHeight + 'px'
          }}
          onClick={this.onOpenChange}
        >
          {/* 内容区 */}
          <div
            className="container"
            style={{
              height: containerHeight + 'px'
            }}
          >
            {this.props.children}
          </div>
        </div>

      </div>
    );
  };

  @executeTimesLogger('FiltrateWraper')
  render() {
    const {
      dataSource,
      moreLists,
      tabLists,
      hasUnfoldIcon,
      isDialogVisible
    } = this.props;
    const { selected } = this.state;

    let noDropDownContent = false;
    if (selected !== '' && selected !== 'more') {
      const itemType = dataSource.get(selected).itemtype;
      if (itemType == 'refer') {
        noDropDownContent = true;
      }
    }


    return (
      <div className="filtrate-wraper">
        <div className="filtrate-title" ref={this.calculateoffsetTop}>
          {this.handleTabs(
            dataSource,
            moreLists,
            tabLists,
            hasUnfoldIcon,
            isDialogVisible
          )}
        </div>

        {noDropDownContent
          ? this.props.children
          : this.generatorMaskContainer(isDialogVisible)}
      </div>
    );
  }
}

function mapStateToProps() {
  return function (state, props) {
    const { uniqueCode } = props;
    const currentStore = state[filtrateNameSpace];
    const dataSourceOfUniqueCode = currentStore.get(uniqueCode);
    if (!currentStore.get(uniqueCode)) {
      return {
        currentStore,
        dataSource: Map({}),
        tabLists: [],
        moreLists: [],
        isDialogVisible: false
      };
    }

    return {
      currentStore,
      dataSource: dataSourceOfUniqueCode.get('$dataSource'),
      tabLists: dataSourceOfUniqueCode.get('$tabLists'),
      moreLists: dataSourceOfUniqueCode.get('$moreLists'),
      isDialogVisible: dataSourceOfUniqueCode.get('isDialogVisible'),
      closeReferDialog: dataSourceOfUniqueCode.get('closeReferDialog')
    };
  };
}

FiltrateWraper.displayName = 'FiltrateWraper';

export default connect(mapStateToProps)(FiltrateWraper);
