import React, { Component } from 'react';
import { Button, List } from 'mobile/base';
import { createSelector, connect, actions, registerNameSpace } from 'core';
import './bottomSelection.less';

class BottomSelection extends Component {
    constructor(props) {
        super(props);
        this.state = { open: false };
    }

    allSelect = () => {
        console.log('全选');
    }

    //点击确定按钮事件
    determine = () => {  //获取当前选中的列表 返回给后台
        const { nameSpaceList } = this.props;
        this.props.handleClick(nameSpaceList.toJS());
    }

    //点击已选事件
    handleSelect = () => { this.setState({ open: !this.state.open }); }

    //点击蒙层关闭事件
    closeModal = () => { this.setState({ open: false }); }

    //点击清空按钮事件
    clearAll = () => {
        const { deleteAll } = actions['SINGLE_MULTIPLE_SELECTION'];
        deleteAll();
    }

    //点击删除单条图标
    clearSingle = (item) => {
        const { deleteValue } = actions['SINGLE_MULTIPLE_SELECTION'];
        deleteValue(item);
    }

    //渲染已选详情弹框
    genratorModal = () => {
        const { nameSpaceList } = this.props; //获取已选择数据
        if (!nameSpaceList.size) {
            return (
                <div className="edit-refer-nodata">
                    <div className="nodata-image" />
                    <div>数据为空，请先选择</div>
                </div>
            );
        } else {
            return (
              <List>
                {nameSpaceList.map((item, index) => {
                    return (
                        <List.Item extra={<i onClick={() => { this.clearSingle(item); }} className='iconfont iconliebiaoshanchu'/>} multipleLine key={index}>
                            {item.refname}
                            <List.Item.Brief>{item.refcode}</List.Item.Brief>
                        </List.Item>
                    );
                })}
              </List>
          );
        }
    }

    render() {
        const { nameSpaceList } = this.props;
        const sidebarMaxHeight = document.documentElement.clientHeight - 180;
        return (
            <div>
                <div className="bottom-selection">
                    <div className='left-extra'>
                        <div className='all' onClick={this.allSelect}>全选</div>
                        <div className='already' onClick={this.handleSelect}>已选{`(${nameSpaceList.size})`}</div>
                    </div>
                    <div className='right-extra'>
                        <Button className='determine-button' disabled={!nameSpaceList.size} onClick={this.determine}>确定</Button>
                    </div>
                </div>
                <div className='select-prop' onClick={(e) => { this.closeModal(e); }} style={{ display: this.state.open ? 'block' : 'none' }}>
                    <div className="inner" style={{ maxHeight: sidebarMaxHeight }} onClick={(e) => { e.stopPropagation(); }}>
                        <div className='model-header'>
                            <div>已选明细</div><button onClick={this.clearAll} disabled={!nameSpaceList.size}>清空</button>
                        </div>
                        <div>{this.genratorModal()}</div>
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps() {
    return function(state, props) {
        const nameSpaceList = state['SINGLE_MULTIPLE_SELECTION'].get('list');
        return { nameSpaceList };
    };
}

export default connect(mapStateToProps)(BottomSelection);

