import React, { Component } from 'react';
import { Card } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Card)
class NCCard extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Card {...this.props} />;
	}
}
export default NCCard;
