import React, { Component } from 'react';

export const echartsWrapper = function (Echarts) {
	return class NCEcharts extends Component {
		constructor(props) {
			super(props);
			this.chartRef = null;
			this.isInitCharts = null;
			this.state = {
				data: null
			};
		}

		componentDidMount = () => {
			this.drawEchartsFun.call(this, this.props);
			window.addEventListener('resize', this.onResize, false);
		};

		componentWillReceiveProps = nextProps => {
			this.drawEchartsFun.call(this, nextProps);
		};

		onResize = () => {
			this.isInitCharts.resize();
		};

		drawEchartsFun = props => {
			const { data } = props;
			if (JSON.stringify(data) !== '{}') {
				this.setState(
					{
						data
					},
					() => {
						this.isInitCharts = Echarts.init(this.chartRef, null, { renderer: 'svg' });
						this.initEcharts();
					}
				);
			}
		};

		initEcharts = () => {
			const { data } = this.state;
			let option = data;
			let setClick = null;

			this.isInitCharts.setOption(option, true);
			this.isInitCharts.off('click');
			this.isInitCharts.off('dblclick');
			this.isInitCharts.on('click', params => {
				clearTimeout(setClick);
				setClick = setTimeout(() => {
					if (this.props.onChartClick) this.props.onChartClick(params);
				}, 250)

			});
			this.isInitCharts.on('dblclick', params => {
				clearTimeout(setClick);
				if (this.props.onChartdblClick) this.props.onChartdblClick(params);
			});
		};

		render() {
			return (
				<div
					ref={r => (this.chartRef = r)}
					style={{
						width: '100%',
						height: '100%'
					}}
				/>
			);
		}
	};
};
