let yonyouJSAPI = {};
const nameList = {
	'hiddenMenu': {}, //隐藏导航条
	'getOAuthCode': {}, //获取appcode
	'QRCodeScan': {}, //扫一扫
	'chooseMap': {}, //选择地图点
	'closeCurrentWebview': {}, //关闭webview页面
	'openNewWebview': {} //打开新的web页面
};

Object.keys(nameList).forEach(fname => {
	yonyouJSAPI[fname] = function (parameters = {}) {
		return new Promise((resolve, reject) => {
			const YonYouJSBridge = window.JSBridge;
			var data = { ...nameList[fname], parameters };
			if (YonYouJSBridge) {
				YonYouJSBridge.do(fname, {
					...data,
					success: (res) => {
						try {
							console.log(`jsapi：${fname}调用成功,返回结果为`, res);
							resolve(res);
						} catch (err) {
							reject({ message: 'jsapi参数序列化失败' });
						}

					},
					fail: error => {
						console.log('jsapi_err', error);
						reject(error);
					}
				});
			} else {
				// reject({message:'jsAPI加载失败'})
				console.log('jsAPI加载失败');
			}
		});
	};
});



export default yonyouJSAPI;
