import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import logger from 'redux-logger';
import { initReducer, actions } from './reducer';

const global = {};

// 平台内部事件
let store = createStore((state = {}) => state, applyMiddleware(logger, thunk));

const { registerNameSpace, removeNameSpace } = initReducer(store);

window.store = store;
store.actions = actions;

export default store;
export { actions, getGlobal, registerNameSpace, removeNameSpace };

function getGlobal() {
	return global;
}
