import React, { Component } from 'react';
import { Range } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Range)
class NCRange extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Range {...this.props} />;
	}
}
export default NCRange;
