import React, { Component } from 'react';
import { ActionSheet } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(ActionSheet)
class NCActionSheet extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <ActionSheet {...this.props} />;
	}
}
export default NCActionSheet;
