/**
     * 进行四舍五入操作
     *
     * @param {*} value
     * @param {*} scale
     * @returns String value
     * @memberof InputNumber
     */
export function ncRounding(value, scale) {
    // 如果没有精度，不需要处理四舍五入
    if (!scale) return value;

    let [_value, _scale] = [value, scale];

    if (!Object.prototype.toString.call(scale) !== '[object Number]' && !isNaN(Number(scale))) _scale = Number(scale);

    // 校验参数
    if (Object.prototype.toString.call(value) !== '[object String]') _value = String(value);

    //移除千分位
    _value = _value.replace(/\,/gi, '');

    const re = /^(\-|\+)?(\d+)?\.?(\d+)?$/;

    if (!re.test(_value)) {
        console.warn('处理参数异常');
        return value;
    }

    // 分割value
    // eslint-disable-next-line no-unused-vars
    let [beforePoint, afterPoint] = _value.split('.');

    // 有小数位数
    if (afterPoint && afterPoint !== '') {
        // 判断小数位数与精度的关系
        if (afterPoint.length > _scale) {
            _value = Number(_value);
            // 进行四舍五入操作
            _value = Number(_value.toFixed(_scale + 1));

            // eslint-disable-next-line no-restricted-properties
            _value = _value * Math.pow(10, _scale);

            _value = Math.round(_value);

            // eslint-disable-next-line no-restricted-properties
            _value = _value / Math.pow(10, _scale);
        }
    }

    _value = addZero(_value, scale);

    return _value;
}

/**
 *进行补零操作
 *
 * @param {*} num
 * @param {*} scale
 * @returns
 * @memberof InputNumber
 */
export function addZero(num, scale) {
    num = String(num);
    num = removeThousands(num);
    if (isNaN(Number(num)) || !num) {
        return '';
    }

    if (scale > 0) {
        let start = num.split('.')[0];
        let end = num.split('.')[1];
        if (!end) {
            end = '';
        }
        let len = end.length;
        if (len < scale) {
            end = end.padEnd(scale, '0');
        }
        return start + '.' + end;
    } else {
        return num;
    }
}

/**
     *移除千分位
     *
     * @param {*} val
     * @returns
     * @memberof InputNumber
     */
export function removeThousands(val) {
    return val ? String(val).replace(/\,/gi, '') : '';
}



/**
 *数字转换成千分位 格式
 *
 * @param {*} num
 * @returns
 * @memberof InputNumber
 */
export function commafy(num) {
    let pointIndex,
        intPart,
        pointPart;
    if (num === '-') {
        return '-';
    }
    if (isNaN(num)) {
        return '';
    }

    num = num + '';
    if (/^.*\..*$/.test(num)) {
        pointIndex = num.lastIndexOf('.');
        intPart = num.substring(0, pointIndex);
        pointPart = num.substring(pointIndex + 1, num.length);
        intPart = intPart + '';
        let re = /(-?\d+)(\d{3})/;
        while (re.test(intPart)) {
            intPart = intPart.replace(re, '$1,$2');
        }
        num = intPart + '.' + pointPart;
    } else {
        num = num + '';
        let re = /(-?\d+)(\d{3})/;
        while (re.test(num)) {
            num = num.replace(re, '$1,$2');
        }
    }
    return num;
}