import React, { Component } from 'react';
import { Progress } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Progress)
class NCProgress extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Progress {...this.props} />;
	}
}
export default NCProgress;
