import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { SearchBar, Modal } from 'mobile/base';
import { NavBar } from 'mobile/containers';
import { registerNameSpace, executeTimesLogger } from 'core';
import { fromJS } from 'immutable';
import { isFunction } from '../../../utils/index';
import { singleMutipleSelection } from './constant'; //获取命名空间
import ClassSelect from './ClassificationSelect/classSelect'; //分类选择组件
import BottomSelection from './BottomFullSelection/bottomSelection'; //底部全选组件
import SingleButton from './SingleElectionButon/singlebutton'; //底部单选确定组件
import SearchModel from './SearchModel/searchModel'; //搜索弹框组件
import SetUpModel from './SetUpModel/setUpModel'; //设置弹框组件
import Single from './SingleReference/single'; //单级参照组件
import MultilevelRefer from './MultilevelRefer'; //多级参照组件
import './index.less';

const namespace = singleMutipleSelection;

const initialvalue = { list: [] };

const { registerAction, dispatch, getState } = registerNameSpace(namespace, fromJS(initialvalue));

registerAction({
	setValue(state, content) {
		dispatch(state.set('list', state.get('list').push(content)));
	},
	deleteValue(state, content) {
		let getList = state.get('list').indexOf(content);
		dispatch(state.set('list', state.get('list').delete(getList)));
	},
	deleteAll(state) {
		dispatch(state.set('list', state.get('list').clear()));
	}
});

class PublicReference extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isShowSearchBar: true,
            classifiedDataSource: [],
			isSearchModel: false,
			isSetUpModel: false,
			isVoice: false
		};
		this.beforeScrollTop = 0;
	};

	renderSearchBar = () => {
		const { isShowSearchBar } = this.state;
		if (isShowSearchBar) {
			return <SearchBar className='search-bar' onFocus={() => { this.searchModel(); }} placeholder={this.props.searchPlaceholder} />;
		}
		return null;
	};

	//弹出搜索model
    searchModel = () => {
    	this.setState({ isSearchModel: !this.state.isSearchModel, isVoice: false });
	}

    setModel = () => {
        this.setState({ isSetUpModel: !this.state.isSetUpModel });
	}

    voice = () => {
        this.setState({ isSearchModel: !this.state.isSearchModel, isVoice: true });
	}

	//滑动事件监听
	onScroll = e => {
        if (!this.props.renderSearch) {
            const spanIcon = document.querySelector('.span-icon') || null;
            const searchPrant = document.getElementById('test-search');
            const searchBar = searchPrant.getElementsByTagName('form')[0];
            let afterScrollTop = e.target.scrollTop;
            if (afterScrollTop > this.beforeScrollTop) {
                if (searchBar) {
                    if (spanIcon) spanIcon.style.display  = 'none';
                    this.setState({ isShowSearchBar: false });
                };
            } else if (!searchBar) {
                if (spanIcon) spanIcon.style.display  = 'block';
                this.setState({ isShowSearchBar: true });
            }
            this.beforeScrollTop = afterScrollTop;
		}
	};

    handleClick = m => {
		//对返回的数据处理 单选返回的是对象 （多级单选中间级可选）多选返回的是 数组
        const { onChange, isRadioListMiddle, isMultiSelectedEnabled } = this.props;
		if (isFunction(onChange)) {
			if (!isMultiSelectedEnabled || isRadioListMiddle) {
                onChange({ value: m, foolValue: { display: m.refname, value: m.refpk } });
			} else {
				const display = m.map((item) => item.refname).join(',');
                const value = m.map((item) => item.refpk).join(',');
                onChange({ value: m, foolValue: { display, value } });
			}
		}
	};

	//底部展示规则
    visibleBottom = () => {
        const { rowEditable, isMultiSelectedEnabled, onChange, renderFooter, isRadioListMiddle } = this.props;
    	if (rowEditable && typeof renderFooter === 'function') {  //判断是否是物料参照（底部由业务渲染）
           return renderFooter({ onConfirm: onChange });
		} else {
    		if (!isMultiSelectedEnabled) {    //判断是单选还是多选
    			if (isRadioListMiddle) {      //为单选时 判断是多级中间级可选 加载底部确定按钮
    				return (<SingleButton handleClick={this.handleClick}/>);
				}
    			return null;
			} else {
    			return (   //多选时候加载统一的底部button
					<div className="name-footer">
						<BottomSelection handleClick={this.handleClick}/>
					</div>
				);
			}
		}
	}

    searchBar = () => {
        const { renderSearch } = this.props;
        if (renderSearch && typeof renderSearch === 'function') {
            return renderSearch();
		} else {
            return (
				<div id="test-search">
					{this.renderSearchBar()}
					<span className='span-icon' onClick={this.voice}><i className="icon iconfont iconyuyin" /></span>
				</div>
			);
		}
	}

	//选择分类掉接口回调到父组件
    classifiedCallback = (data) => {
		this.setState({ classifiedDataSource: data });
	}

	@executeTimesLogger('PublicReference')
	render() {
		const { visible, isMultistage, refTypeUrl } = this.props;
		const navBarConfig = {
			goBack: {
				beforeEve: () => {
					this.props.onCancel();
					return true;
				}
			},
			title: this.props.modelTitle,
            iconAreas: [{ icon: 'iconshezhi', callBack: () => { this.setModel(); } }],
		};
		return (
			<div>
                <Modal className="model-style" visible={visible} transparent>
                    <div className="name-body">
                        <div className="name-header">
                            <NavBar {...navBarConfig} />
							{this.searchBar()}
                            {refTypeUrl ? <ClassSelect {...this.props} classifiedCallback={this.classifiedCallback} /> : null}
                        </div>
                        <div className="name-content" onScroll={this.onScroll}>
                            {isMultistage ? <MultilevelRefer handleClick={this.handleClick} {...this.props}/> : <Single classifiedDataSource={this.state.classifiedDataSource} handleClick={this.handleClick} {...this.props} />}
                        </div>
                        {this.visibleBottom()}
                    </div>
                </Modal>
                <SearchModel onModelCancel={this.searchModel} {...this.props} isVoice={this.state.isVoice} isSearchModel={this.state.isSearchModel}/>
                <SetUpModel onCancel={this.setModel} {...this.props} isSetUpModel={this.state.isSetUpModel}/>
			</div>
		);
	}
}

PublicReference.propTypes = {
	visible: PropTypes.bool, //参照model是否弹出
	isMultistage: PropTypes.bool, //单级还是多级(默认单级选择)
	isMultiSelectedEnabled: PropTypes.bool, //(单级单选||单级多选)(默认单级单选)
	isStyleUpAndDown: PropTypes.bool, //单选分为三种样式(此属性为上下行显示)
	isStyleLeftAndRight: PropTypes.bool, //单选分为三种样式(此属性为左右两边显示)
	refUrl: PropTypes.string, //参照查询url
	refTypeUrl: PropTypes.string, //参照类型查询url
	modelTitle: PropTypes.string, //弹框显示文本
	searchPlaceholder: PropTypes.string, //搜索框显示Placeholder
	rowEditable: PropTypes.bool, // 可编辑参照(物料参照)
	onChange: PropTypes.func, //单级单选回调事件
	onCancel: PropTypes.func, //导航栏取消事件返回
	renderFooter: PropTypes.func, // 物料参照渲染 footer
	isRadioListMiddle: PropTypes.bool, //多级参照（单选中间级可选）
	isCheckboxListMiddle: PropTypes.bool, //多级参照（多选中间级可选）
    queryCondition: PropTypes.func, //参照过滤查询条件 返回对象
    rootHeadings: PropTypes.object, //多级参照导航标题初始值
    pageSize: PropTypes.number, //分页一页显示多少条
	renderSearch: PropTypes.func, //物料参照替换searchBar
};

PublicReference.defaultProps = {
	visible: false, //参照model是否弹出
	isMultistage: false, //单级还是多级(默认单级选择)
	isMultiSelectedEnabled: false, //(单选||多选)(默认单选)
	isStyleUpAndDown: true, //单选分为三种样式(此属性为上下行显示)
	isStyleLeftAndRight: false, //单选分为三种样式(此属性为左右两边显示)
	refUrl: '', //参照查询url
    refTypeUrl: '', //参照类型查询url
	modelTitle: '参照', //弹框显示文本
	searchPlaceholder: '名称、编码', //搜索框显示Placeholder
	rowEditable: false, // 可编辑参照(物料参照)
	isRadioListMiddle: false, //多级参照（单选中间级可选）
	isCheckboxListMiddle: false, //多级参照（多选中间级可选）
    rootHeadings: { referPk: 'root', title: '使用部门' },  //多级参照导航标题初始值
    pageSize: 10, //分页一页显示多少条
};

export default PublicReference;
