import axios from 'axios';
import ReactDOM from 'react-dom';
import { isFunction } from 'core/utils';
import { Toast } from 'mobile/base';
import { callAction } from '../utils/maInterface';

const loginURL = '/nccloud/mob/platform/mob/login';
const getLoginInfo = () => {
	const loginInfo = sessionStorage.getItem('loginInfo');
	return loginInfo ? JSON.parse(loginInfo) : null;
};
const setLoginInfo = loginInfo => {
	sessionStorage.setItem('loginInfo', JSON.stringify(loginInfo));
};

//通过ma连接的方式
class connectWithMA {
	constructor(param) {
		if (isFunction(param.data)) {
			param.data = param.data();
		}
		this.param = param;
	}

	send() {
		callAction(this.param);
	}
}
//通过直连的方式
class connectWithoutMA {
	constructor(param) {
		this.param = param;
	}

	send() {
		const loginInfo = getLoginInfo();
		if (loginInfo) {
			this.connect();
		} else {
			try {
				if (window.ENV && window.ENV.LOGIN_INFO) {
					this.submit(window.ENV.LOGIN_INFO);
				} else {
					this.preAjax();
				}
			} catch (err) {
				this.preAjax();
			}
		}
	}

	connect() {
		const { param } = this;
		const loginInfo = getLoginInfo();
		const token = loginInfo.data;
		const appCode = loginInfo.appCode;
		Object.assign(param.headers, { accessToken: token });
		param._data = {
			busiParamJson: JSON.stringify(isFunction(param.data) ? param.data() : param.data),
			sysParamJson: {
				sysParamJson: {
					busiaction: '移动登录',
					appCode,
					langCode: 'simpchn',
					ts: new Date().getDate()
				}
			}
		};
		this.asyncAxios(param);
	}

	preAjax() {
		let div = window.document.createElement('div');
		window.document.body.appendChild(div);
		// ReactDOM.render(<Login submit={this.submit} />, div);
		//ReactDOM.render(<div>123</div>, div);
	}

	submit = value => {
		const that = this;
		that.loginNCC(value).then(() => {
			that.send();
		});
	};

	loginNCC(value) {
		const { code, userCode, appCode } = value;
		const busiParamJson = {
			code: code || 'develop',
			userCode,
			appId: '1234',
			appSecret: '2345'
		};
		return new Promise((resolve, reject) => {
			axios({
				url: loginURL,
				data: {
					busiParamJson: JSON.stringify(busiParamJson),
					sysParamJson: {
						busiaction: '移动登录',
						appCode,
						langCode: 'simpchn',
						ts: new Date().getDate()
					}
				},
				method: 'post'
			})
				.then(res => {
					if (res.status === 200 && res.data) {
						setLoginInfo({
							...res.data,
							appCode
						});
						resolve();
					} else {
						Toast.fail(res.message);
					}
				})
				.catch(res => {
					let data = res;
				});
		});
	}

	asyncAxios(param) {
		const { method, headers, url, _data, success, error } = param;
		axios({
			method,
			headers,
			url,
			data: _data,
			withCredentials: true,
			transformRequest: [
				function(data) {
					let gData = JSON.stringify(data) || '';
					return gData;
				}
			],
			transformResponse: [
				function(data) {
					return data;
				}
			]
		})
			.then(res => {
				const response = JSON.parse(res.data);
				if (response.code == 200) {
					success(response);
				} else {
					Toast.fail(response.message);
				}
			})
			.catch(res => {
				let data = res;
				error(data, url);
			});
	}
}

const ajax = function(param) {
	const defaultparam = {
		url: '/',
		method: 'post',
		data: {},
		success(res) {
			console.log(res);
		},
		error(res) {
			console.error(res);
			let msgContent = JSON.stringify(res.message);
			console.log(msgContent);
		},
		headers: { 'Content-Type': 'application/json;charset=UTF-8' },
		isMA: true
	};
	const _param = { ...defaultparam, ...param };

	if (window.ENV && window.ENV.ISMA) {
		const connect = new connectWithMA(_param);
		connect.send();
	} else {
		const connect = new connectWithoutMA(_param);
		connect.send();
	}
};
export { ajax };
