import React, { Component } from 'react';
import { InputItem } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(InputItem)
class NCInputItem extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <InputItem {...this.props} />;
	}
}
export default NCInputItem;
