import React, { Component } from 'react';
import { Picker } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Picker)
class NCPicker extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Picker {...this.props} />;
	}
}
export default NCPicker;
