/* eslint-disable no-unused-vars */
import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom';
import {
	InputItem,
	TextareaItem,
	Checkbox,
	Radio,
	Picker,
	Switch,
	List,
	Modal,
	Button,
	DatePicker,
	Calendar,
	Stepper
} from '../base';

import ReferLoader from '../containers/Refer/ReferLoader';

const CheckboxItem = Checkbox.CheckboxItem;
const RadioItem = Radio.RadioItem;
const ListItem = List.Item;

const popupType = ['checkbox', 'radio'];
const pickerType = ['select'];
const calenderType = ['rangepicker'];
const dateOrTimeType = ['datepicker', 'datetimepicker', 'timepicker', 'TZDatePickerEnd'];
const switchType = ['switch', 'switch_browse', 'checkbox_switch'];
const stepperType = ['stepper'];
const triggerAsFocusType = ['input', 'number', 'textarea'];

/**
 * 根据 meta 中 item 相关信息渲染 form 元素组件
 * @param {Object} itemToRender meta 中待渲染 item
 * @param {Object} config 其他配置参数, 如 beforeEvent, afterEvent
 */
class RenderItem extends Component {
	constructor(props) {
		super(props);
		this.state = {
			dialogVisible: false,
			currentValue: '',
			currentDisplay: '',
			currentValueTemp: '',
			radioCheckedValue: '',
			localItemValue: {
				diplay: null,
				value: null
			},
			optionsLocal: []
		};
		this.inputRef = null;
	}

	// 校验 valueObject.value 值是否有效
	valueObjectValid = valueObject => {
		let valid = true;
		if (Object.prototype.toString.call(valueObject) === '[object Object]') {
			if ([undefined, 'null'].includes(JSON.stringify(valueObject.value))) {
				valid = false;
			}
		} else {
			valid = false;
		}
		return valid;
	};

	/**
   * binding event
   */
	onChangeHandler = val => {
		let shouldProceed = true;
		let resultingValue;
		let resultingDisplay;
		const { radioCheckedValue, optionsLocal } = this.state;
		const {
			options,
			isMultiSelectedEnabled,
			config,
			itemtype, // 组件类型
			attrcode, // 属性编码
			onChangeEvent
		} = this.props;
		if (itemtype === 'radio') {
			options.map(option => {
				if (option.value === radioCheckedValue) {
					resultingValue = option.value;
					resultingDisplay = option.display;
				}
			});
		} else if (popupType.includes(itemtype)) {
			let currentValues = [];
			let currentDisplays = [];
			optionsLocal.map(option => {
				if (option.checked) {
					currentValues.push(option.value);
					currentDisplays.push(option.display);
				}
			});
			resultingValue = currentValues;
			resultingDisplay = currentDisplays.join(',');
		} else if (pickerType.includes(itemtype)) {
			resultingValue = Array.isArray(val) ? val[0] : val;
			options.some(option => {
				if (option.value === resultingValue) {
					resultingDisplay = option.display;
					return true;
				}
				return false;
			});
		} else if (dateOrTimeType.includes(itemtype)) {
			resultingValue = this.formatDate(val);
		} else if (calenderType.includes(itemtype)) {
			resultingValue = val.join(',');
		} else if (itemtype === 'refer') {
			const foolValue = val.foolValue || {};
			resultingValue = foolValue.value;
			resultingDisplay = foolValue.display;
		} else {
			resultingValue = val;
		}
		const callbackValue = {
			value: Array.isArray(resultingValue) ? resultingValue.join(',') : resultingValue,
			display: Array.isArray(resultingDisplay) ? resultingDisplay.join(',') : resultingDisplay || resultingValue
		};
		this.changePanelVisible(false, true);
		if (typeof onChangeEvent === 'function') {
			onChangeEvent(attrcode, callbackValue);
		}
		if (!triggerAsFocusType.includes(itemtype)) {
			this.onAfterEventHandler(val);
		}
	};

	onFocusHandler = () => {
		this.onBeforeEventHandler();
	};

	onBeforeEventHandler = e => {
		const { attrcode: attrCode, itemValue: oldValue, itemtype, config, moduleId } = this.props;
		const callbackConfig = { moduleId, attrCode, oldValue, itemInfo: { ...this.props, ...config } };
		const { onBeforeEvent } = this.props.config;
		// 获焦失焦类型 item
		if (triggerAsFocusType.includes(itemtype)) {
			if (typeof onBeforeEvent === 'function') {
				if (onBeforeEvent(callbackConfig) === false) {
					this.triggerItemBlur();
				}
			} else if (Object.prototype.toString.call(onBeforeEvent) === '[object Promise]') {
				onBeforeEvent(callbackConfig).catch(e => this.triggerItemBlur);
			}
		} else {
			// 其他类型
			if (typeof onBeforeEvent === 'function') {
				if (onBeforeEvent(callbackConfig) === true) {
					this.changePanelVisible(true);
				}
			} else if (Object.prototype.toString.call(onBeforeEvent) === '[object Promise]') {
				onBeforeEvent(callbackConfig).then(e => this.changePanelVisible(true));
			} else {
				this.changePanelVisible(true);
			}
		}
	};

	onAfterEventHandler = newVal => {
		const { config, attrcode: attrCode, itemtype, moduleId } = this.props;
		const { onAfterEvent } = config;
		const { localItemValue: oldValue } = this.state;
		if (typeof onAfterEvent === 'function') {
			let newValue;
			if (triggerAsFocusType.concat(stepperType).includes(itemtype)) {
				// if (!stepperType.includes(itemtype)) {
				newValue = { display: newVal, value: newVal };
				// }
			} else if (dateOrTimeType.includes(itemtype)) {
				const timeStr = this.formatDate(newVal);
				newValue = { display: timeStr, value: timeStr };
			} else if (newVal) {
				if (itemtype === 'refer') {
					const newFoolValue = newVal.foolValue || {};
					newValue = {
						display: newFoolValue.display,
						value: newFoolValue.value
					};
				} else {
					newValue = {
						display: newVal.display,
						value: newVal.value
					};
				}
			}
			onAfterEvent({
				moduleId,
				attrCode,
				oldValue,
				newValue,
				rawValue: newVal,
				itemInfo: {
					...this.props,
					...config
				}
			});
			this.updateLocalItemValue(newValue);
		}
	};

	triggerItemBlur = () => {
		if (this.inputRef && typeof this.inputRef.blur === 'function') {
			this.inputRef.blur();
		}
	};

	onBlurHandler = val => {
		const { onBlurEvent } = this.props.config;
		let eventValue = val;
		if (typeof onBlurEvent === 'function') {
			onBlurEvent(eventValue);
		}
	};

	formatDate = date => {
		const { itemtype } = this.props;
		const pad = n => (n < 10 ? `0${n}` : n);
		const dateStr = `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(date.getDate())}`;
		const timeStr = `${pad(date.getHours())}:${pad(date.getMinutes())}`;
		switch (itemtype) {
			case 'datepicker':
			case 'TZDatePickerEnd':
			case 'rangepicker':
				return dateStr;
			case 'datetimepicker':
				return `${dateStr} ${timeStr}`;
			case 'timepicker':
				return timeStr;
		}
	};

	initializeCurrentValue = nextProps => {
		const props = nextProps || this.props;
		let { currentValue, currentDisplay } = props;
		const { itemtype, options, attrcode } = props;

		/**
     * initiating value
     */
		if (switchType.includes(itemtype)) {
			currentValue = currentValue || false;
		}
		if (popupType.includes(itemtype)) {
			if ([undefined, null].includes(currentValue)) {
				currentValue = '';
			} else if (typeof currentValue !== 'string') {
				currentValue = currentValue + '';
			}
			currentValue = currentValue.split(',').filter(val => val);
			let currentDisplays = [];
			options.forEach(option => {
				option.checked = currentValue.includes(option.value + '');
				if (option.checked) {
					currentDisplays.push(option.display);
				}
			});
			currentDisplay = currentDisplays.join(',');
		}
		if (pickerType.includes(itemtype) && currentValue) {
			options.some(option => {
				if (option.value === currentValue) {
					currentDisplay = option.display;
					return true;
				}
				return false;
			});
			currentValue = [currentValue];
		}
		if (itemtype === 'timepicker' && currentValue) {
			const hoursAndMinnutes = currentValue.split(':');
			currentValue = new Date(null, null, null, hoursAndMinnutes[0], hoursAndMinnutes[1]);
			currentDisplay = this.formatDate(currentValue);
		} else if (dateOrTimeType.includes(itemtype) && currentValue) {
			currentValue = new Date(currentValue);
			currentDisplay = this.formatDate(currentValue);
		}
		if (calenderType.includes(itemtype)) {
			currentValue = currentValue || '';
			const currentValues = currentValue
				.split(',')
				.map(time => new Date(time))
				.filter(time => time.toString() !== 'Invalid Date');
			if (currentValues.length) {
				currentValue = currentValues;
				currentDisplay = currentValue.map(date => this.formatDate(date)).join(',');
			} else {
				currentValue = null;
				currentDisplay = null;
			}
		}
		if (stepperType.includes(itemtype) && !currentValue) {
			currentValue = 0;
		}
		if (!['refer'].concat(popupType).includes(itemtype)) {
			currentDisplay = currentDisplay || currentValue;
		}

		return {
			currentValue,
			currentDisplay
		};
	};

	changePanelVisible = (visible, closedFromConfirm) => {
		const { itemtype, options, disabled, editable, currentValue, formStatus } = this.props;
		// 整单不为编辑态, 或当前项为禁用状态或浏览态时, 不显示panel
		if (formStatus === 'browse' || disabled || editable === false) {
			return;
		}
		let stateToSet = {};
		stateToSet.dialogVisible = visible;
		if (visible && Array.isArray(options)) {
			let currentValues = Array.isArray(currentValue) ? currentValue : [];
			currentValues = currentValues.map(val => val + '');
			options.map(option => {
				const itemChecked = currentValues.includes(option.value + '');
				if (itemtype === 'radio' && itemChecked) {
					stateToSet.radioCheckedValue = option.value;
				}
				option.checked = itemChecked;
			});
			this.setState({ optionsLocal: options });
		}
		if (!visible && !closedFromConfirm && itemtype === 'radio') {
			// 点击遮罩或取消触发的关闭情况下, 重置选中值为初始态
			stateToSet.radioCheckedValue = currentValue || '';
		}
		this.setState(stateToSet);
	};

	checkboxConfirm = () => {
		this.onChangeHandler();
	};

	onCalendarRangeConfirm = (startTime, endTime) => {
		const rangeArray = [this.formatDate(startTime), this.formatDate(endTime)];

		this.onChangeHandler(rangeArray);
	};

	initRadioChecked = props => {
		const {
			itemtype, // 组件类型
			currentValue // 初始值
		} = props;
		if (itemtype === 'radio') {
			this.setState({
				radioCheckedValue: currentValue
			});
		}
	};

	/**
	 * nextProps
	 */
	updateLocalItemValue = newValue => {
		let valueToSet;
		if (Object.prototype.toString.call(newValue) === '[object Object]') {
			valueToSet = newValue;
		} else {
			let { currentValue, currentDisplay } = this.initializeCurrentValue();
			valueToSet = { display: currentDisplay, value: currentValue };
		}
		try {
			valueToSet = JSON.parse(JSON.stringify(valueToSet));
		} catch (error) {
			valueToSet = { display: null, value: null };
		}
		const { localItemValue } = this.state;
		if (localItemValue.value !== valueToSet.value) {
			if (valueToSet.value == null || valueToSet.value === undefined) {
				valueToSet.display = null;
			}
			this.setState({
				localItemValue: valueToSet
			});
		}
	};

	componentWillReceiveProps(nextProps) {
		this.initRadioChecked(nextProps);
		// this.updateLocalItemValue(nextProps);
	}

	componentDidMount() {
		this.initRadioChecked(this.props);
		this.updateLocalItemValue();
	}

	render() {
		const {
			config,
			itemtype, // 组件类型
			attrcode, // 属性编码
			label, // 属性名
			disabled, // 禁用
			isEdit: editable, // 编辑态
			options, // checkbox, radio, select 选项
			maxlength,
			isMultiSelectedEnabled, // 是否多选
			required,
			visible,
			/**
       * for number
       */
			scale,
			min,
			max,
			step,
			/**
       * for textarea
       */
			rows,
			labelLevel,

			placeholder,
			formStatus,
			moduleId,

			/**
			 * for refer
			 */
			// refUrl,
			// refName,
			// refcode,
			...otherProps
		} = this.props;
		const { dialogVisible, radioCheckedValue } = this.state;
		const {
			// 模块 id
			hasLabel = true,
			hideDivider = false,
			...otherConfig
		} = config;

		// 不可见或配置了labelLevel(label, sublabel for multiple refer)
		if (visible === false || labelLevel) {
			return null;
		}
		let maxLength;
		if (typeof maxlength === 'number') {
			maxLength = maxlength;
		} else if (typeof maxlength === 'string' && !isNaN(parseInt(maxlength))) {
			maxLength = parseInt(maxlength);
		}

		let FormItem = null;
		let displayedLabel = (hasLabel && label) || ' ';
		let classToPass = hideDivider === true ? 'hide-divider' : '';
		classToPass += ` nc-form-item nc-form-item-${formStatus} form-item-${attrcode}`;
		let { currentValue, currentDisplay } = this.initializeCurrentValue();
		if (!currentDisplay || currentDisplay === '请选择') {
			classToPass += ' placeholder';
		}
		if (!hasLabel) {
			classToPass += ' hide-label';
		}
		if (formStatus === 'browse' || editable === false || disabled === true) {
			return (
				<ListItem wrap={true} className={classToPass} extra={currentDisplay || currentValue || ' '}>
					{displayedLabel}
				</ListItem>
			);
		}
		if (required) {
			displayedLabel = (
				<div>
					{displayedLabel}
					<span className="required-tag">*</span>
				</div>
			);
		}
		if (!dateOrTimeType.concat('refer').includes(itemtype)) {
			classToPass += ' nc-form-item-inner';
		}
		if (!itemtype) {
			return null;
		}

		/**
     * common attributes
     */
		let propsToPass = {
			disabled: formStatus === 'disabled' || disabled,
			className: classToPass,
			onChange: val => this.onChangeHandler(val)
		};
		if (triggerAsFocusType.concat('stepper', 'label').includes(itemtype)) {
			propsToPass = {
				...propsToPass,
				maxLength,
				value: currentValue,
				clear: true,
				placeholder: placeholder || '请输入' + label,
				onFocus: this.onBeforeEventHandler,
				onBlur: val => this.onAfterEventHandler(val),
				ref: dom => {
					this.inputRef = ReactDOM.findDOMNode(dom);
					this.inputRef = this.inputRef && this.inputRef.querySelector('input');
				}
			};
		}
		if (itemtype === 'textarea') {
			propsToPass.ref = dom => {
				this.inputRef = ReactDOM.findDOMNode(dom);
				this.inputRef = this.inputRef && this.inputRef.querySelector('textarea');
			};
		}
		if (['number', 'stepper'].includes(itemtype)) {
			propsToPass = {
				...propsToPass,
				min,
				max
			};
		}
		if (switchType.includes(itemtype)) {
			propsToPass.checked = currentValue;
		}
		if (dateOrTimeType.includes(itemtype)) {
			if (disabled) {
				classToPass += ' form-item-disabled';
			}
			propsToPass = {
				...propsToPass,
				format: this.formatDate,
				value: currentValue,
				className: classToPass
			};
		}
		if (dateOrTimeType.concat(pickerType, popupType, calenderType, 'refer').includes(itemtype)) {
			if (!currentDisplay || (Array.isArray(currentDisplay) && currentDisplay.length === 0)) {
				currentDisplay = placeholder || '请选择' + label;
			}
			propsToPass.placeholder = currentDisplay || '请选择' + label;
		}
		switch (itemtype) {
			case 'password':
				propsToPass.type = 'password';
				break;
			case 'number':
				propsToPass = {
					...propsToPass,
					// type: 'money',
					// moneyKeyboardAlign: 'left',
					type: 'digit',
					scale
				};
				break;
			case 'stepper':
				propsToPass = {
					...propsToPass,
					showNumber: true,
					step: step || 1
				};
				break;
			case 'textarea':
				if (displayedLabel) {
					propsToPass.title = displayedLabel;
				}
				propsToPass.rows = 3;
				break;
			case 'label':
				propsToPass.editable = false;
				break;
			case 'datepicker':
			case 'TZDatePickerEnd':
				propsToPass.mode = 'date';
				break;
			case 'timepicker':
				propsToPass.mode = 'time';
				break;
			case 'select':
				propsToPass = {
					...propsToPass,
					value: currentValue || null,
					cascade: false,
					col: 1
				};
				break;
			case 'refer':
				if (typeof isMultiSelectedEnabled === 'boolean') {
					propsToPass.isMultiSelectedEnabled = isMultiSelectedEnabled;
				}
				propsToPass = {
					...propsToPass,
					visible: dialogVisible,
					data: {
						display: propsToPass.placeholder,
						value: currentValue
					},
					...otherProps,
					onCancel: e => this.changePanelVisible(false)
				};
				break;
			default:
				break;
		}

		/**
     * render item
     */
		let optionsToRender = [];
		switch (itemtype) {
			case 'input':
			case 'password':
			case 'number':
			case 'label':
				FormItem = <InputItem {...propsToPass}>{displayedLabel}</InputItem>;
				break;
			case 'stepper':
				FormItem = (
					<div className={propsToPass.className}>
						<ListItem
							extra={
								<div>
									<Stepper {...propsToPass} style={{ minWidth: '100px' }} />
								</div>
							}
						>
							{displayedLabel}
						</ListItem>
					</div>
				);
				break;
			case 'textarea':
				FormItem = (
					<div className="form-item-textarea-container">
						<TextareaItem {...propsToPass} />
						{typeof maxLength === 'number' && (
							<div className="counter">
								<span className={currentValue && currentValue.length ? 'not-empty' : ''}>
									{(currentValue && currentValue.length) || 0}
								</span>
								<span>/{maxLength}</span>
							</div>
						)}
					</div>
				);
				break;
			case 'switch':
				FormItem = (
					<div className={propsToPass.className}>
						<ListItem extra={<Switch {...propsToPass} />}>{displayedLabel}</ListItem>
					</div>
				);
				break;
			case 'datepicker':
			case 'TZDatePickerEnd':
			case 'datetimepicker':
			case 'timepicker':
				FormItem = (
					<div className={propsToPass.className + ' date-form-item'}>
						<DatePicker {...propsToPass}>
							<List>
								<ListItem
									arrow="horizontal"
									extra={propsToPass.placeholder}
									className="nc-form-item-inner"
									// className={propsToPass.className}
									// className={propsToPass.disabled ? 'form-item-disabled' : ''}
								>
									{displayedLabel}
								</ListItem>
							</List>
						</DatePicker>
					</div>
				);
				break;
			case 'rangepicker':
				const defaultValueObject =
					Array.isArray(currentValue) && currentValue.length ? { defaultDate: currentValue } : {};
				// const now = new Date();
				FormItem = (
					<Fragment>
						<ListItem
							// className={propsToPass.disabled ? 'form-item-disabled' : ''}
							className={propsToPass.className}
							extra={propsToPass.placeholder}
							arrow="horizontal"
							onClick={this.onBeforeEventHandler}
						>
							{displayedLabel}
						</ListItem>
						<Calendar
							visible={dialogVisible}
							onCancel={() => this.changePanelVisible(false)}
							onConfirm={this.onCalendarRangeConfirm}
							// onSelectHasDisableDate={this.onSelectHasDisableDate}
							// getDateExtra={this.getDateExtra}
							// defaultDate={[new Date(+now - 86400000), new Date(+now - 345600000)]}
							{...defaultValueObject}
						/>
					</Fragment>
				);
				break;
			case 'checkbox':
			case 'radio':
				const { disabled: itemDisabled, className, ...otherPropsToPass } = propsToPass;
				const { optionsLocal } = this.state;
				optionsToRender =
					Array.isArray(optionsLocal) &&
					optionsLocal.map(option => {
						return itemtype === 'radio' ? (
							<RadioItem
								{...otherPropsToPass}
								key={option.value}
								checked={option.value === radioCheckedValue}
								disabled={itemDisabled || option.disabled}
								className={className}
								onChange={e => {
									options.map(opt => (opt.checked = opt === option));
									this.setState({
										radioCheckedValue: option.value
									});
								}}
							>
								{option.display}
							</RadioItem>
						) : (
							<CheckboxItem
								{...otherPropsToPass}
								key={option.value}
								defaultChecked={option.checked}
								disabled={itemDisabled || option.disabled}
								onChange={e => {
									let opts = JSON.parse(JSON.stringify(optionsLocal));
									opts.map(opt => {
										if (opt.value === option.value) {
											opt.checked = !opt.checked;
										}
									});
									this.setState({ optionsLocal: opts });
								}}
								className={option.checked ? 'checked ' + className : className}
							>
								{option.display}
							</CheckboxItem>
						);
					});
				FormItem = (
					<Fragment>
						<ListItem
							extra={propsToPass.placeholder}
							arrow="horizontal"
							onClick={this.onBeforeEventHandler}
							className={className}
							// className={propsToPass.disabled ? 'form-item-disabled' : ''}
						>
							{displayedLabel}
						</ListItem>
						<Modal
							popup
							visible={dialogVisible}
							onClose={e => this.changePanelVisible(false)}
							animationType="slide-up"
						>
							<List>
								{optionsToRender}
								<ListItem>
									<Button inline size="small" onClick={e => this.changePanelVisible(false)}>
										取消
									</Button>
									<Button type="primary" inline size="small" onClick={this.onChangeHandler}>
										确认
									</Button>
								</ListItem>
							</List>
						</Modal>
					</Fragment>
				);
				break;
			case 'select':
				optionsToRender = options.filter(option => option.disabled !== true).map(option => {
					option.label = option.display;
					return option;
				});
				FormItem = (
					<Picker {...propsToPass} data={[optionsToRender]}>
						<ListItem arrow="horizontal" className={propsToPass.className}>
							{displayedLabel}
						</ListItem>
					</Picker>
				);
				break;
			case 'refer':
				FormItem = (
					<List className={propsToPass.className + ' refer-form-item'}>
						<ListItem
							extra={propsToPass.placeholder}
							arrow="horizontal"
							onClick={this.onBeforeEventHandler}
							className="nc-form-item-inner"
						>
							{displayedLabel}
						</ListItem>
						<ReferLoader {...config} {...propsToPass} />
					</List>
				);
				break;
			default:
				break;
		}

		return FormItem;
	}
}

export default RenderItem;
