import React, { Component } from 'react';
import { ImagePicker } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(ImagePicker)
class NCImagePicker extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <ImagePicker {...this.props} />;
	}
}
export default NCImagePicker;
