import React, { Component } from 'react';
import { NavBar as AntdNavBar, Icon, Popover } from 'mobile/base';
import { closeWebView as closeweb } from 'mobile/utils/method';
import './index.less';
import { isFunction } from 'util';

const Item = Popover.Item;

class NCNavBar extends Component {
	constructor(props) {
		super(props);
	}

	state = {
		visible: false,
		selected: ''
	};

	// 渲染左侧按钮区
	renderLeftContent = () => {
		const { closeWebview, goBack } = this.props;
		let leftIcons = [<i className="icon iconfont iconfanhui big" onClick={this.backParent} />];
		if (goBack) {
			const { title, beforeEve } = goBack;
			if (title) {
				leftIcons = [
					<i onClick={this.backParent.bind(this, beforeEve)} className="icon-text">
						{title}
					</i>
				];
			} else {
				leftIcons = [
					<i className="icon iconfont iconfanhui big" onClick={this.backParent.bind(this, beforeEve)} />
				];
			}
		}

		if (closeWebview) {
			const { beforeEve } = closeWebview;
			leftIcons.push(
				<i
					className="icon iconfont iconguanbi icon-margin-left big"
					onClick={this.closeWebview.bind(this, beforeEve)}
				/>
			);
		}
		return leftIcons;
	};

	// 渲染功能区
	renderPopover = (operations, selectedEve) => {
		const alignConfig = {
			overflow: { adjustY: -20, adjustX: 0 },
			offset: [-10, 0]
		};
		return (
			<Popover
				key={'renderPopover'}
				mask
				overlayClassName="fortest"
				overlayStyle={{ color: 'currentColor' }}
				visible={this.state.visible}
				overlay={operations}
				align={alignConfig}
				onVisibleChange={this.handleVisibleChange}
				onSelect={(opt, e) => {
					this.onSelect(opt, selectedEve, e);
				}}
			>
				<div className="peration-btn">
					<i className="icon iconfont icongengduo big" />
				</div>
			</Popover>
		);
	};
	// 渲染右侧区域
	renderRightContent = () => {
		const { iconAreas, operationAreas } = this.props;

		let renderRightContent, renderOperationAreas;

		if (iconAreas && Object.prototype.toString.call(iconAreas) == '[object Array]') {
			// 渲染右侧外面icon
			renderRightContent = [...iconAreas];

			renderRightContent = iconAreas.map((item, index) => {
				const { icon, callBack } = item;
				return (
					<i
						className={`icon iconfont ${icon} icon-margin-right big`}
						onClick={e => {
							if (isFunction(callBack)) callBack(e);
						}}
					/>
				);
			});
		}
		if (operationAreas) {
			const { items, selectedEve } = operationAreas;
			if (items && Object.prototype.toString.call(items) == '[object Array]') {
				// 渲染操作区Item
				renderOperationAreas = items.map((item, index) => {
					const { value, label, icon } = item;

					return (
						<Item key={index} value={value} data-seed="logId" style={item.style}>
							<i className={`icon iconfont ${icon}`} />
							<span>{label}</span>
						</Item>
					);
				});
				const popover = this.renderPopover(renderOperationAreas, selectedEve);
				renderRightContent.push(popover);
			}
		}

		return renderRightContent;
	};

	operationItem = callBack => {
		if (isFunction(callBack)) {
			callBack();
		}
	};
	// 左箭头函数
	backParent = (beforeEve, event) => {
		if (isFunction(beforeEve)) {
			if (beforeEve(event)) return;
		}
		history.back();
	};

	closeWebview = () => {
		closeweb();
	};

	// 气泡成员选择回调
	onSelect = (opt, selectedEve, e) => {
		if (isFunction(selectedEve)) {
			selectedEve(opt,e);
		}

		this.setState({
			visible: false,
			selected: opt.props.value
		});
	};

	// 气泡显隐控制
	handleVisibleChange = visible => {
		this.setState({
			visible
		});
	};

	render() {
		const { title = '' } = this.props;
		return (
			<div className="nav-bar-content">
				<AntdNavBar
					mode="light"
					onLeftClick={this.onLeftClick}
					leftContent={this.renderLeftContent()}
					rightContent={this.renderRightContent()}
					className="nav-bar"
				>
					<div className="nav-title">{title}</div>
				</AntdNavBar>
			</div>
		);
	}
}
export const NavBar = NCNavBar;
