import React, { Component } from 'react';
import { Badge } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Badge)
class NCBadge extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<span className="nc-badge">
				<Badge {...this.props} />
			</span>
		);
	}
}
export default NCBadge;
