/* eslint-disable one-var-declaration-per-line */
/* eslint-disable no-unused-vars */
/* eslint-disable react/no-multi-comp */
/* eslint-disable import/no-unresolved */
import React, { Component } from 'react';
import store, { registerNameSpace } from 'core/store';
import { connect } from 'core';

import { fromJS, List, Map } from 'immutable';
import { ListView } from '../../base';
import { createForm } from '../Form';
import { LIST_NAMESPACE } from './constants';

const { registerAction, dispatch, getState } = registerNameSpace(LIST_NAMESPACE);

registerAction({
	/**
	 * 获取 moduleIds 对应的表单数据
	 * @param {*} state
	 * @param {String | Array} moduleIds 表单id或id数组
	 * @returns {Object} {
	 *  moduleId1: [{
	 *    itemValue: {
	 *      value: 1, display: 1
	 *    },
	 *    ...otherProps
	 *  }
	 * }]
	 */
	getListData(state, moduleId) {
		return (state.getIn([moduleId, 'listData']) || List()).toJS();
	},

	/**
	 * 
	 * @param {immutable} state 
	 * @param {Object} data 
	 */
	setListData(state, { moduleId, data }) {
		return dispatch(state.setIn([moduleId, 'listData'], fromJS(data)));
	},

	concatListData(state, { moduleId, data }) {
		const storedList = state.getIn([moduleId, 'listData']) || List();
		return dispatch(state.setIn([moduleId, 'listData'], storedList.concat(fromJS(data))));
	}
});

class ListContainer extends Component {
	constructor(props) {
		super(props);
		this.state = {
			// refreshing: false, // 下拉刷新
			// loading: false, // 上拉加载
			height: props.containerHeight || document.documentElement.clientHeight + 'px' // 容器高度
			// height: props.containerHeight || document.documentElement.clientHeight - 50 + 'px'
		};
		const { moduleId, config } = props;
		const { data } = config;
		let listData;
		if (List.isList(data)) {
			listData = data;
		} else if (Array.isArray(data)) {
			listData = fromJS(data);
		} else {
			listData = List();
		}
		dispatch(getState().setIn([moduleId, 'listData'], listData));
		this.dataSource = new ListView.DataSource({ rowHasChanged: (row1, row2) => row1 !== row2 });
	}
	// componentDidMount() {
	// 未传容器高度时, 扣除容器距顶部距离
	// if (!this.props.containerHeight) {
	// 	const actualHeight = this.state.height - ReactDOM.findDOMNode(this.ptrEl).offsetTop;
	// 	setTimeout(() => {
	// 		this.setState({
	// 			height: actualHeight
	// 		});
	// 	}, 0);
	// }
	// }

	onRefreshHandler = () => {
		// console.log('list refreshing');
		const { config: { onRefresh }, moduleId } = this.props;
		if (typeof onRefresh === 'function') {
			const result = onRefresh({ moduleId });
			if (result instanceof Promise) {
				result.catch(err => console.log(`${moduleId} onRefresh 发生错误, ${err}`));
			}
		}
	};

	onEndReachedHandler = () => {
		// console.log('loading more');
		const { config: { onLoadMore }, moduleId } = this.props;
		if (typeof onLoadMore === 'function') {
			const result = onLoadMore({ moduleId });
			if (result instanceof Promise) {
				result.catch(err => console.log(`${moduleId} onLoadMore 发生错误, ${err}`));
			}
		}
	};

	render() {
		const { moduleId, metaItems, moduleType, listItems, config } = this.props;
		// const { height, refreshing, loading } = this.state;
		const { onRefresh, onEndReached } = config;
		if (metaItems.size === 0) {
			// console.log(`${moduleId} 模板尚未加载, createList 暂不渲染`);
			return null;
		}
		if (moduleType !== 'table') {
			console.log(`createList 只支持 moduletype 为 table 的模板, ${moduleId} 模板类型为 ${moduleType}`);
			return null;
		}
		const dataSource = this.dataSource.cloneWithRows(listItems.toJS());
		return (
			<ListView
				// ref={el => (this.lv = el)}
				dataSource={dataSource}
				renderRow={row => this.renderRow(row)}
				// style={{
				// 	height
				// }}
				onRefresh={onRefresh}
				onEndReached={onEndReached}
				{...this.props}
			/>
		);
	}

	renderRow = row => {
		const { config: { renderRowHeader, renderRowBody, renderRowFooter, onRowClick }, moduleId } = this.props;
		return (
			<div
				onClick={() => {
					if (typeof onRowClick === 'function') {
						onRowClick({ moduleId, rowData: row });
					}
				}}
				className="nc-form-module-container"
			>
				{typeof renderRowHeader === 'function' && renderRowHeader({ moduleId, rowData: row })}
				{typeof renderRowBody === 'function' ? (
					renderRowBody({ moduleId, rowData: row })
				) : (
					createForm(moduleId, {
						formId: row.rowid,
						status: 'browse',
						data: row,
						hideDivider: true,
						containerClassName: 'nc-list-module-container',
						customNamespace: LIST_NAMESPACE
					})
				)}
				{typeof renderRowFooter === 'function' && renderRowFooter({ moduleId, rowData: row })}
			</div>
		);
	};
}

ListContainer = connect(function() {
	return function(state, props) {
		const { moduleId } = props;
		const metaStored = store.getState()['meta'] || Map();
		return {
			metaItems: metaStored.getIn([moduleId, 'items']) || List(),
			moduleType: metaStored.getIn([moduleId, 'moduletype']),
			listItems: getState().getIn([moduleId, 'listData']) || List(),
			...props
		};
	};
})(ListContainer);

/**
 * @description: 创建列表
 * @param  {String} moduleId
 * @param  {Object} config
 * 		{
				data: List() || Array,
				containerHeight: document.documentElement.clientHeight,
				renderRowHeader({moduleId, rowData}) {
					return (<div />);
				},
				renderRowFooter({moduleId, rowData}) {
					return (<div />);
				},
				onEndReached() {
					return Promise instance || others
				},
				onRefresh() {}
 * 		}
 */
export default function createList(moduleId, config) {
	return <ListContainer moduleId={moduleId} config={config} />;
}
