import React, { Component } from 'react';
import { NoticeBar } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(NoticeBar)
class NCNoticeBar extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <NoticeBar {...this.props} />;
	}
}
export default NCNoticeBar;
