import React, { Component } from 'react';
// eslint-disable-next-line import/no-unresolved
import { actions, connect, createSelector, pureUpdate, executeTimesLogger } from 'core';
import { List } from 'immutable';
import RenderItem from '../../utils/renderItem';
import { FORM_NAMESPACE, MODULE_SEPARATOR } from './constants';

class FormItem extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	shouldComponentUpdate(nextProps, nextState) {
		return pureUpdate.call(this, nextProps, nextState);
	}

	handleChange = (attrcode, value) => {
		// const state = getState();
		const { setValueByAttrcode } = actions[FORM_NAMESPACE];
		const { moduleId, formId } = this.props;
		const formModuleId = __getFormModuleId(moduleId, formId);
		setValueByAttrcode({ attrcode, value, moduleId, formModuleId });
	};

	getJSOptions = () => {
		const jsOptions = this.props.options.toJS();
		return jsOptions.map(opt => {
			const optVal = opt.value;
			if (!isNaN(optVal)) {
				opt.value = optVal + '';
			}
			return opt;
		});
	};

	@executeTimesLogger('FormItem')
	render() {
		const { metaItem } = this.props;
		const jsOptions = this.getJSOptions();
		return (
			<RenderItem {...metaItem.toJS()} {...this.props} options={jsOptions} onChangeEvent={this.handleChange} />
		);
	}
}
/**
 * createSelectors
 */
// itemValue
const makeGetValue = defaultValue => {
	return createSelector((byMeta, byRow, byForm) => {
		if (byForm !== undefined) {
			return byForm;
		}
		if (byRow !== undefined) {
			return byRow;
		}
		if (byMeta !== undefined) {
			return byMeta;
		}
		return defaultValue;
	});
};

const mapStateToProps = () => {
	const getValue = makeGetValue('');
	// const getCurrentDisplay = makeGetValue('');
	const getScale = makeGetValue(-1);
	const getArray = makeGetValue(List());
	const getDefaultFalseBool = makeGetValue(false);
	const getDefaultTrueBool = makeGetValue(true);
	const getFormStatus = makeGetValue('edit');

	return (state, props) => {
		const { namespace = FORM_NAMESPACE, formId, metaItem, moduleId, config } = props;
		const { [namespace]: storedNamespace, meta: storedMeta } = state;
		const formModuleId = __getFormModuleId(moduleId, formId);
		const formStatus = getFormStatus(
			storedMeta.getIn([moduleId, 'areastatus']),
			storedNamespace.getIn([formModuleId, 'areaStatus'])
		);
		const attrcode = metaItem.get('attrcode');
		const itemtype = metaItem.get('itemtype');
		const visibleField = config.visibleField || 'visible';
		const getInRowItem = (...paths) => {
			return storedNamespace.getIn([formModuleId, 'rowData', 'values', attrcode].concat(paths));
		};
		const getInFormItem = (...paths) => {
			// console.log('** form: ', formModuleId, storedNamespace.getIn([formModuleId, 'formItems', attrcode]));
			return storedNamespace.getIn([formModuleId, 'formItems', attrcode].concat(paths));
		};
		const getCurrentValue = () => {
			let currentValue = getValue(
				metaItem.getIn(['initialvalue', 'value']),
				getInRowItem('value'),
				getInFormItem('itemValue', 'value')
			);
			if (!['number'].includes(itemtype) && ![null, undefined].includes(currentValue)) {
				currentValue += '';
			}
			return currentValue;
		};
		return {
			currentValue: getCurrentValue(),
			currentDisplay: getValue(
				metaItem.getIn(['initialvalue', 'display']),
				getInRowItem('display'),
				getInFormItem('itemValue', 'display')
			),
			scale: getScale(metaItem.get('scale'), getInRowItem('scale')),
			label: metaItem.get('label'),
			attrcode,
			itemtype,
			options: getArray(metaItem.get('options')),
			isMultiSelectedEnabled: metaItem.get('isMultiSelectedEnabled'),
			disabled: getDefaultFalseBool(
				metaItem.get('disabled'),
				getInRowItem('disabled'),
				getInFormItem('disabled')
			),
			required: getDefaultFalseBool(
				metaItem.get('required'),
				getInRowItem('required'),
				getInFormItem('required')
			),
			editable: getDefaultTrueBool(metaItem.get('editable'), getInRowItem('editable'), getInFormItem('editable')),
			visible: getDefaultTrueBool(
				metaItem.get(visibleField),
				getInRowItem(visibleField),
				getInFormItem(visibleField)
			),
			maxlength: metaItem.get('maxlength'),
			max: metaItem.get('max'),
			min: metaItem.get('min'),
			formStatus
		};
	};
};

export default connect(mapStateToProps, null)(FormItem);

function __getFormModuleId(moduleId, formId) {
	return formId ? [moduleId, formId].join(MODULE_SEPARATOR) : moduleId;
}
