import React, { Component } from 'react';
import { ActivityIndicator } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(ActivityIndicator)
class NCActivityIndicator extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <ActivityIndicator {...this.props} />;
	}
}
export default NCActivityIndicator;
