import React, { Component } from 'react';
import { NavBar } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(NavBar)
class NCNavBar extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <NavBar {...this.props} />;
	}
}
export default NCNavBar;
