import React, { Component } from 'react';
import { Calendar } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';
import './style.less';

@extendsSmb(Calendar)
class NCCalendar extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Calendar {...this.props} />;
	}
}
export default NCCalendar;
