import { JSAPI } from 'mobile/api/jsapi';

const nativeHeaderHidden = () => {
	JSAPI.hiddenMenu();
};
const closeWebView = () => {
	JSAPI.closePage();
};
const openWebView = url => {
	JSAPI.openWindow({ orientation: '0', url });
};
const  getQueryString = (name) => { 
	var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i'); 
	var r = window.location.search.substr(1).match(reg); 
	if (r != null) return unescape(r[2]); 
	return null; 
}; 
export { nativeHeaderHidden, closeWebView, openWebView, getQueryString };
