import React, { Component } from 'react';
import { SwipeAction } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(SwipeAction)
class NCSwipeAction extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <SwipeAction {...this.props} />;
	}
}
export default NCSwipeAction;
