import React, { Component } from 'react';
import { Button } from 'mobile/base';
import { createSelector, connect, actions, registerNameSpace } from 'core';
import './singlebutton.less';

class Singlebutton extends Component {
    constructor(props) {
        super(props);
    }

    determine = () => {  //获取当前选中的列表 返回给后台
        const { nameSpaceList } = this.props;
        this.props.handleClick(...nameSpaceList.toJS());
    }

    render() {
        const { nameSpaceList } = this.props;
        return (
            <div className="bottom-selection">
                <Button disabled={!nameSpaceList.size} className='determine-button' onClick={this.determine}>确定</Button>
            </div>
        );
    }
}

function mapStateToProps() {
    return function(state) {
        const nameSpaceList = state['SINGLE_MULTIPLE_SELECTION'].get('list');
        return { nameSpaceList };
    };
}

export default connect(mapStateToProps)(Singlebutton);

