/* 检测类型是否为数组 */
export function isArray(param) {
    return Object.prototype.toString.call(param).slice(8, -1) === 'Array';
}

/* 检测类型是否为字符串 */
export function isString(param) {
    return Object.prototype.toString.call(param).slice(8, -1) === 'String';
}

/* 检测类型是否为函数 */
export function isFunction(param) {
    return Object.prototype.toString.call(param).slice(8, -1) === 'Function';
}

/* 检测类型是否为时间类型 */
export function isDate(param) {
    return Object.prototype.toString.call(param).slice(8, -1) === 'Date';
}

/* 检测类型是否为数字 */
export function isNumber(param) {
    return (
        Object.prototype.toString.call(param).slice(8, -1) === 'Number' &&
        !Number.isNaN(param)
    );
}

// genrator rowId
export function getRandom() {
    return String(new Date().getTime()).slice(-5) + Math.random().toString(12);
}
//判断数组,对象,字符串是否为空
export function isEmpty(param) {
    let result = false;

    if (typeof param === 'object') {
        let hasvalue = false;
        if (Array.isArray(param)) {
            hasvalue = param.length !== 0;
        } else {
            for (var { } in param) {
                hasvalue = true;
                break;
            }
        }

        result = !hasvalue;
    } else if (typeof param === 'string') {
        if (param === '') result = true;
    } else if (param === undefined) {
        result = true;
    }
    return result;
}
