/*
* @method   节流方法
* @author   add by xuyangt @19/03/08
* @params 
*     callback     {fun}      回调函数
*     delay     {number}   延时执行时间
* @return   {fun}          
* @demo     
*/
export function throttle(callback, interval = 0) {
    let last = 0;
    return function () {
        let param = arguments;
        let that = this;
        let now = +new Date();
        if (now - last >= interval) {
            last = now;
            typeof callback === 'function' && callback.apply(that, param);
        }
    };
}
