import React, { Component } from 'react';
import { SearchBar } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(SearchBar)
class NCSearchBar extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <SearchBar {...this.props} />;
	}
}
export default NCSearchBar;
