import React, { Component } from 'react';
import { Menu } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Menu)
class NCMenu extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Menu {...this.props} />;
	}
}
export default NCMenu;
