import React, { Component } from 'react';
import { Icon } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Icon)
class NCIcon extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Icon {...this.props} />;
	}
}
export default NCIcon;
