import React, { Component } from 'react';
import { Grid } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Grid)
class NCGrid extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Grid {...this.props} />;
	}
}
export default NCGrid;
