import React, { Component } from 'react';
import { Switch } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

import { isFunction } from 'util';

@extendsSmb(Switch)
class NCSwitch extends Component {
	constructor(props) {
		super(props);
		const { checked = true } = props;
		this.state = {
			checked
		};
	}

	handleOnChange = checked => {
		const { onChangeEve } = this.props;
		if (isFunction(onChangeEve)) {
			onChangeEve(checked);
		}
		this.setState({
			checked: !this.state.checked
		});
	};

	handleOnClick = checked => {
		const { ononClickEve } = this.props;
		if (isFunction(ononClickEve)) {
			ononClickEve(checked);
		}
	};

	render() {
		const { color = '#E14C46' } = this.props;
		return (
			<span className="nc-switch">
				<Switch
					checked={this.state.checked}
					onClick={this.handleOnClick}
					onChange={this.handleOnChange}
					color={color}
				/>
			</span>
		);
	}
}
export default NCSwitch;
