import React, { Component } from 'react';
import { Flex } from 'antd-mobile';

import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Flex)
class NCFlex extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		return <Flex {...this.props} />;
	}
}
export default NCFlex;
