import React, { Component } from 'react';
import { Toast } from 'antd-mobile';
import './index.less';
import extendsSmb from '../../utils/extendsSmb';

@extendsSmb(Toast)
class NCToast extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return <Toast {...this.props} />;
	}
}
export default NCToast;
