/* eslint-disable import/no-unresolved */
import { timeFunctionTranslater } from 'mobile/utils/timer';

export default {
    /**
     *获取所有conditions（数据）
     *
     * @param {*} state
     * @param {*} uniqueCode  组件唯一id
     * @returns 返回业务组需要的所有字段的conditions
     */
    getAllconditions(state, uniqueCode) {
        if (!uniqueCode) {
            throw new Error('uniqueCode is required');
        }

        function handleCondition(condition, itemtype) {
            if (itemtype === 'rangepicker') {
                const { value } = condition;
                const rangepickerVal = JSON.parse(JSON.stringify(condition));
                for (let key in value) {
                    const item = value[key];
                    if (/^#.+#$/.test(item)) {
                        rangepickerVal.value[key] = timeFunctionTranslater(item);
                    }
                }
                return rangepickerVal;
            }

            return condition;
        }

        const storeOfuniqueCode = state.get(uniqueCode);
        const dataSource = storeOfuniqueCode.get('$dataSource');
        const finalRes = {
            logic: 'and',
            conditions: []
        };

        dataSource.map(item => {
            let publicParams = {
                datatype: item.datatype || '',
                field: item.attrcode,
                oprtype: item.oprtype || '=',
                display: '',
                value: {
                    firstvalue: '',
                    secondvalue: ''
                }
            };

            if (item.condition) {
                finalRes.conditions.push({
                    ...publicParams,
                    ...handleCondition(item.condition, item.itemtype)
                });
            } else if (item.initialvalue) {
                finalRes.conditions.push({
                    ...publicParams,
                    ...handleCondition(
                        {
                            value: {
                                firstvalue: item.initialvalue.value,
                                secondvalue: ''
                            }
                        },
                        item.itemtype
                    )
                });
            } else {
                finalRes.conditions.push(publicParams);
            }
        });

        console.log('the res is', finalRes);

        return finalRes;
    }
};
